import React, { Component } from 'react';
import { Button, Tooltip } from '@blueprintjs/core';
import styled from 'styled-components';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { Col, Row } from 'react-flexbox-grid';
import { VSelectField } from 'gsi-vx-ui';
import { FieldState, FormState } from 'formstate';

const fees: any[] = [];
const datafees: any[] = [];
export const FOrderFeeContext = React.createContext({
  onSelectFee: (e: any) => {},
  onDelFee: (e: any) => {},
  listFees: fees,
  dataFee: fees,
  isLoading: false
});

const WraperActions = styled(Row)`
  padding: 5px;
`;

interface IPropsActions extends InjectedIntlProps {
  element: any;
}

class OrderDetailsFeesActions extends Component<IPropsActions> {
  form!: FormState<any>;
  constructor(props: IPropsActions) {
    super(props);
    this.form = new FormState({
      fieldselect: new FieldState('')
    });
  }
  render() {
    const { intl } = this.props;
    return (
      <FOrderFeeContext.Consumer>
        {value => (
          <WraperActions>
            <Col xs={6}>
              <Tooltip
                usePortal
                hoverCloseDelay={0}
                position={'bottom'}
                content={intl.formatMessage({
                  id: 'delivery.order.open.addFees',
                  defaultMessage: 'Add Feed'
                })}
              >
                <div style={{ marginTop: 15 }}>
                  <VSelectField
                    fieldState={this.form.$.fieldselect}
                    id={'idfieldselectfee'}
                    onChange={value.onSelectFee}
                    fill
                    inline
                    minimal
                    options={value.listFees}
                    icon={'plus'}
                    iconOnly
                  />
                </div>
              </Tooltip>
            </Col>
            <Col xs={6}>
              <div style={{ marginTop: 15 }}>
                {!this.props.element ? (
                  <Button
                    disabled={!this.props.element}
                    onClick={() => {
                      value.onDelFee(this.props.element);
                    }}
                    icon={'trash'}
                    fill
                    minimal
                  />
                ) : (
                  <Tooltip
                    usePortal
                    position={'bottom'}
                    hoverCloseDelay={0}
                    content={intl.formatMessage({
                      id: 'delivery.order.open.deleteFees',
                      defaultMessage: 'Remove Feed'
                    })}
                  >
                    <Button
                      disabled={!this.props.element}
                      onClick={() => {
                        value.onDelFee(this.props.element);
                      }}
                      icon={'trash'}
                      fill
                      minimal
                    />
                  </Tooltip>
                )}
              </div>
            </Col>
          </WraperActions>
        )}
      </FOrderFeeContext.Consumer>
    );
  }
}

export default injectIntl(OrderDetailsFeesActions);
