import { Menu, MenuItem, Button } from '@blueprintjs/core';
import styled from 'styled-components';
import { VSelectField } from 'gsi-vx-ui';

export interface StyledMenuProps {
  active?: boolean;
  background?: any;
  color?: any;
  padding?: any;
  height?: string;
  className?: string;
}

export const StyledMenuItem = styled(MenuItem)`
  ${(props: StyledMenuProps) => {
    const { active, background, color } = props;
    return active
      ? `background: ${background}!important;
         color: ${color}!important;`
      : '';
  }}
  margin-bottom: 2px;
`;

export const StyledMenu = styled(Menu)`
  ${(props: StyledMenuProps) => {
    const { padding, height } = props;
    const style_padding = padding ? `padding: ${padding}!important;` : '';
    const style_height = `height:${height ? height : '150px'};`;
    return `
        ul {
          border: none;
          padding: 0;
          box-shadow: none;
          margin: -10px;
        }
  
  ${style_padding} 
          ${style_height}
          overflow:overlay;`;
  }}
`;
export const WhiteButton = styled(Button)`
  color: white !important;
  & > span {
    color: white !important;
  }
`;
export const BlackButton = styled(Button)`
  color: black !important;
  & > span {
    color: black !important;
  }
`;
export const WhiteSelect = styled(VSelectField)`
  & {
    color: white !important;
  }
`;

interface IHeaderVEditableList {
  textcolor: string;
}
export const HeaderVEditableList = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    fill: ${(props: IHeaderVEditableList) => props.textcolor && props.textcolor} !important;
  }
`;
