import React, { Component } from 'react';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { Button, Classes, Dialog } from '@blueprintjs/core';
import { inject, observer } from 'mobx-react';
import { OrderManagementStore } from '../../../stores/OrderManagementStore';
import { VCardPanel } from 'gsi-vx-ui';
import CustomComponent from '../../../commons/CustomComponent';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';
import SearchAccount from '../search_account/SearchAccount';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { styleButton } from '../style';

const ThemeConsumer = AppThemeContext.Consumer;
const OrderConsumer = OrderContext.Consumer;

interface IDialogAccount extends InjectedIntlProps {
  order_management_store?: OrderManagementStore;
}
interface IDialogAccountState {
  open: boolean;
}
@inject('order_management_store')
@observer
class DialogCreateAccount extends Component<IDialogAccount, IDialogAccountState> {
  constructor(props: IDialogAccount) {
    super(props);
  }
  public state: IDialogAccountState = {
    open: false
  };
  changeOpen = () => {
    this.setState({ open: !this.state.open });
  };
  render() {
    return (
      <React.Fragment>
        <Button icon={'plus'} onClick={this.changeOpen} style={styleButton}>
          {this.props.intl.formatMessage({ id: 'delivery.order.open.btn_create' })}
        </Button>
        <Dialog
          isOpen={this.state.open}
          onClose={this.changeOpen}
          style={{ width: '85%' }}
          canOutsideClickClose={false}
          canEscapeKeyClose={false}
        >
          <ThemeConsumer>
            {(themeContext: ITheme) =>
              themeContext && (
                <VCardPanel
                  headerText={this.props.intl.formatMessage({ id: 'delivery.order.open.searchCriteria' })}
                  bodyPadding={'1px'}
                  headerBackgroundColor={themeContext.headers}
                  headerColor={'#FFFFFF'}
                  headerTextJustify={'center'}
                  headerCustomComponent={<CustomComponent action={this.changeOpen} />}
                >
                  <div className={Classes.DIALOG_BODY}>
                    <OrderConsumer>
                      {(orderContext: IOrderContext) =>
                        orderContext && (
                          <SearchAccount
                            onSaveOrder={() => {
                              orderContext.handleOnSaveOrders();
                            }}
                            onClose={this.changeOpen}
                          />
                        )
                      }
                    </OrderConsumer>
                  </div>
                </VCardPanel>
              )
            }
          </ThemeConsumer>
        </Dialog>
      </React.Fragment>
    );
  }
}

export default injectIntl(DialogCreateAccount);
