import React, { Component } from 'react';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { Button, Dialog } from '@blueprintjs/core';
import { inject, observer } from 'mobx-react';
import { OrderManagementStore } from '../../../stores/OrderManagementStore';
import { VCardPanel, VSuspenseLoading } from 'gsi-vx-ui';
import CustomComponent from '../../../commons/CustomComponent';
import FilterFormComponent from '../filter_component/FilterFormComponent';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { styleButton } from '../style';

const ThemeConsumer = AppThemeContext.Consumer;

interface IDialogFilter extends InjectedIntlProps {
  order_management_store?: OrderManagementStore;
}
interface IDialogFilterState {
  open: boolean;
}
@inject('order_management_store')
@observer
class DialogFilterComponent extends Component<IDialogFilter, IDialogFilterState> {
  constructor(props: IDialogFilter) {
    super(props);
  }
  public state: IDialogFilterState = {
    open: false
  };
  changeOpen = () => {
    this.setState({ open: !this.state.open });
  };
  handleSaveFilters = (data: any) => {
    if (data.profileId && data.profileId !== -1) {
      this.props.order_management_store!.updateProfile(data);
    } else {
      data.profileId = null;
      this.props.order_management_store!.saveProfile(data);
    }
    this.changeOpen();
  };
  render() {
    const {
      listZone,
      listUrgency,
      listTypes,
      listProducts,
      isLoading,
      listProfiles,
      advancedProfiles
    } = this.props.order_management_store!;
    return (
      <React.Fragment>
        <Button style={styleButton} icon={'filter'} onClick={this.changeOpen}>
          {this.props.intl.formatMessage({ id: 'delivery.order.open.btn_filter' })}
        </Button>
        <Dialog
          isOpen={this.state.open}
          canOutsideClickClose={false}
          usePortal
          onClose={this.changeOpen}
          style={{ width: '85%', paddingBottom: '0' }}
          canEscapeKeyClose={false}
        >
          <ThemeConsumer>
            {(themeContext: ITheme) =>
              themeContext && (
                <VCardPanel
                  headerText={this.props.intl.formatMessage({ id: 'delivery.order.open.titleFilter' })}
                  bodyPadding={'1px'}
                  headerBackgroundColor={themeContext.headers}
                  headerColor={'#FFFFFF'}
                  headerTextJustify={'center'}
                  headerCustomComponent={<CustomComponent action={this.changeOpen} />}
                >
                  <VSuspenseLoading>
                    <FilterFormComponent
                      onSave={this.handleSaveFilters}
                      productList={listProducts}
                      zoneslist={listZone}
                      isLoading={isLoading}
                      typeslist={listTypes}
                      listProfiles={listProfiles}
                      advancedProfiles={advancedProfiles}
                      urgencylist={listUrgency}
                    />
                  </VSuspenseLoading>
                </VCardPanel>
              )
            }
          </ThemeConsumer>
        </Dialog>
      </React.Fragment>
    );
  }
}

export default injectIntl(DialogFilterComponent);
