import styled from 'styled-components';
import { VCheckboxField, VInputField } from 'gsi-vx-ui';
import { Col } from 'react-flexbox-grid';
import { Button } from '@blueprintjs/core';

export const SVCheckboxField = styled(VCheckboxField)`
  color: #ffffff;
`;

export const styleButton = { marginBottom: 5, width: 200 };
export const SCol = styled(Col)`
  display: flex;
  justify-content: flex-start;
`;
export const SButtonDown = styled(Button)`
  @media screen and (min-width: 650px) {
    position: relative;
    right: 76px;
  }
  margin-right: 5px;
  height: 30px;
`;
export const SVInputField = styled(VInputField)`
  width: 100% !important;
  margin: 0px 0px 0px 10px;
  !important & label {
    padding-top: 1px;
    padding-left: 4px;
    width: 100% !important;
    display: flex;
    flex-direction: row-reverse;
  }
  & input {
    width: 100% !important;
    margin: 0px !important;
  }
`;
