import React, { Component } from 'react';
import { disposeOnUnmount, inject, observer } from 'mobx-react';
import { Dialog } from '@blueprintjs/core';
import { VCardPanel, VSpinner } from 'gsi-vx-ui';
import ListDuplicateComponent from './ListDuplicateComponent';
import { RiftRouter } from 'rift-router';
import { observable, reaction } from 'mobx';
import { DuplicateOrdersStore } from '../../../stores/DuplicateOrdersStore';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import CustomComponent from '../../../commons/CustomComponent';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import OrderDetailsComponent from '../order_details/OrderDetailsComponent';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';

const OrderConsumer = OrderContext.Consumer;

const ThemeConsumer = AppThemeContext.Consumer;

interface IDuplicatesComponent extends InjectedIntlProps {
  router?: RiftRouter;
  filter: boolean;
  days: number | string;
  duplicate_orders_store?: DuplicateOrdersStore;
}

@inject('router', 'duplicate_orders_store')
@observer
class DuplicatesComponent extends Component<IDuplicatesComponent> {
  @observable
  activeDrop: boolean;

  constructor(props: IDuplicatesComponent) {
    super(props);
    if (this.props.router!.search.hasOwnProperty('drop')) {
      this.activeDrop = this.props.router!.search.drop === 'duplicate';
    } else {
      this.activeDrop = false;
    }
    this.onChangeFilter();
    this.onChangeDays();
    disposeOnUnmount(this, [
      reaction(
        () => this.props.filter,
        () => {
          this.onChangeFilter();
        }
      ),
      reaction(
        () => this.props.days,
        () => {
          this.onChangeDays();
        }
      )
    ]);
  }
  onChangeDays = () => {
    this.props.days
      ? this.props.duplicate_orders_store!.addFilter({
          value: this.props.days,
          field: 'numberDate',
          operator: 'EQUALITY'
        })
      : this.props.duplicate_orders_store!.removeFilter({
          value: '',
          field: 'numberDate',
          operator: 'EQUALITY'
        });
  };
  onSelectOrder = (data: any) => {
    const { duplicate_orders_store } = this.props;
    duplicate_orders_store!.setElement(data);
  };
  loadOrders = () => {
    this.props.duplicate_orders_store!.getElements();
  };
  componentWillUnmount() {
    this.props.duplicate_orders_store!.resetElement();
    this.props.duplicate_orders_store!.setOpenDetails(false);
  }
  handleSaveDetails = () => {
    const { duplicate_orders_store } = this.props;
    duplicate_orders_store!.setOpenDetails(false);
    duplicate_orders_store!.getElements();
  };
  onChangeFilter = () => {
    this.props.filter && this.props.duplicate_orders_store!.updateFilterType();
    this.props.duplicate_orders_store!.setFilterBySummary(this.props.filter);
  };
  render() {
    const { duplicate_orders_store } = this.props;
    const { elements, count, isLoading, element, openDetails } = duplicate_orders_store!;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <OrderConsumer>
              {(orderContext: IOrderContext) =>
                orderContext && (
                  <React.Fragment>
                    <VCardPanel
                      headerText={`Duplicate Orders: (${count} of ${orderContext.totalOrders})`}
                      bodyPadding={'1px'}
                      headerTextJustify={'center'}
                      headerBackgroundColor={themeContext.headers}
                      headerColor={'#FFFFFF'}
                      defaultActive={this.activeDrop}
                    >
                      {isLoading ? (
                        <VSpinner />
                      ) : (
                        <ListDuplicateComponent
                          columns={[
                            'accountId',
                            'name',
                            'orderId',
                            'orderStatus',
                            'type',
                            'productName',
                            'orderDate',
                            'qty',
                            'lastDate',
                            'lastDelQty',
                            'lastDelDriver'
                          ]}
                          renderCustomerType={orderContext.renderCustomerType}
                          orders={elements}
                          onRowClick={this.onSelectOrder}
                        />
                      )}
                    </VCardPanel>
                    {openDetails && (
                      <Dialog
                        isOpen={true}
                        canOutsideClickClose={false}
                        onClose={() => duplicate_orders_store!.setOpenDetails(false)}
                        style={{ width: '85%', paddingBottom: '0' }}
                        canEscapeKeyClose={false}
                      >
                        <VCardPanel
                          headerText={this.props.intl.formatMessage({ id: 'delivery.order.open.titleDetails' })}
                          bodyPadding={'1px'}
                          headerBackgroundColor={themeContext.headers}
                          headerColor={'#FFFFFF'}
                          headerTextJustify={'center'}
                          headerCustomComponent={
                            <CustomComponent action={() => duplicate_orders_store!.setOpenDetails(false)} />
                          }
                        >
                          <OrderDetailsComponent
                            orderDetails={duplicate_orders_store!.element}
                            onSave={this.handleSaveDetails}
                            onCancel={() => duplicate_orders_store!.setOpenDetails(false)}
                            nofees={0}
                          />
                        </VCardPanel>
                      </Dialog>
                    )}
                  </React.Fragment>
                )
              }
            </OrderConsumer>
          )
        }
      </ThemeConsumer>
    );
  }
}

export default injectIntl(DuplicatesComponent);
