import React, { Component } from 'react';
import { VCardPanel, VSpinner, VTable } from 'gsi-vx-ui';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';
import { upperCase } from 'lodash';

const OrderConsumer = OrderContext.Consumer;

const ThemeConsumer = AppThemeContext.Consumer;

interface IListUnGeoOrders extends InjectedIntlProps {
  noungeoorders: number;
  noorders: number;
  orders: any[];
  columns: string[];
  isLoading: boolean;
  onSelect: (data: any) => void;
}

class ListOpenOrdersComponent extends Component<IListUnGeoOrders> {
  constructor(props: IListUnGeoOrders) {
    super(props);
  }
  render() {
    const { noungeoorders, noorders, orders, isLoading, intl } = this.props;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              headerText={`${intl.formatMessage({
                id: 'delivery.order.open.title_list_ungeo'
              })} (${noungeoorders} of ${noorders})`}
              bodyPadding={'1px'}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#FFFFFF'}
              headerTextJustify={'center'}
            >
              {isLoading ? (
                <VSpinner />
              ) : (
                <OrderConsumer>
                  {(orderContext: IOrderContext) =>
                    orderContext && (
                      <VTable
                        columns={this.props.columns}
                        typeHeightRow={'HALF'}
                        striped
                        enableColumnResizing={true}
                        cellSelectionType={'ENTIRE_ROW'}
                        actionsSelection={{ onSelectionChange: this.onSelectRow }}
                        widgetsCell={orderContext.renderCustom(this.props.columns)}
                        columns_name={{
                          orderId: this.props.intl.formatMessage({ id: 'delivery.order.review.cmTickNo' }),
                          accountId: this.props.intl.formatMessage({ id: 'delivery.order.review.cmAccId' }),
                          lastDate: this.props.intl.formatMessage({ id: 'delivery.order.review.cmLastDate' }),
                          orderDate: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_date' })),
                          geo: this.props.intl.formatMessage({ id: 'delivery.order.open.cmGeo' }),
                          type: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_type' })),
                          name: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.name' })),
                          qty: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_qty' }))
                        }}
                        data={orders}
                      />
                    )
                  }
                </OrderConsumer>
              )}
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }

  onSelectRow = (data: any) => {
    this.props.onSelect(data);
  };
}

export default injectIntl(ListOpenOrdersComponent);
