import React, { Component } from 'react';
import { Classes, Button, Tooltip } from '@blueprintjs/core';
import { VCardPanel, VSelectField } from 'gsi-vx-ui';
import { HeaderVEditableList, StyledMenu, StyledMenuItem, WhiteButton, WhiteSelect } from './style';
import { FieldState, FormState } from 'formstate';
import { ElevationType, IEditableItemsList, IEditableListProps } from './IEditableList';

const styleDiv = {};
interface IEditableListState {
  listSelected: IEditableItemsList[];
  listItems: IEditableItemsList[];
  listSelect: IEditableItemsList[];
}

export class VEditableList extends Component<IEditableListProps, IEditableListState> {
  form!: FormState<any>;
  constructor(props: IEditableListProps) {
    super(props);
    this.initForm();
  }
  public state: IEditableListState = {
    listSelected: this.props.elementsSelected.filter(e => e.active),
    listItems: this.props.elementsSelected,
    listSelect: this.props.listSelectElements
  };
  componentWillReceiveProps(nextProps: IEditableListProps) {
    this.setState({
      ...this.state,
      listSelected: nextProps.elementsSelected.filter(e => e.active),
      listItems: nextProps.elementsSelected,
      listSelect: nextProps.listSelectElements
    });
  }
  isActive = (elements: IEditableItemsList) => {
    return !!this.state.listSelected.find(e => e.value === elements.value);
  };
  onItemClick = (active: boolean, element: IEditableItemsList) => {
    const { listSelected } = this.state;
    let new_list: IEditableItemsList[];
    active
      ? (new_list = listSelected.filter(e => e.value !== element.value))
      : (new_list = listSelected.concat(element));
    this.setState({ ...this.state, listSelected: new_list });
    this.props.onItemClick && this.props.onItemClick(new_list);
  };
  onSlectItem = (i: any) => {
    const listItem = this.props.listSelectElements.find(e => e.value === i);
    const new_list = this.state.listItems;
    const values: any[] = [];
    if (!!listItem) {
      const new_listSelect = this.state.listSelect.filter(e => e.value !== i);
      new_list.push(listItem);
      this.setState({ ...this.state, listItems: new_list, listSelect: new_listSelect });
    }
    new_list.map(l => {
      values.push(l.value);
    });
    this.props.onSelect(values);
  };
  onDeleteItem = () => {
    const new_list: any[] = [];
    const add_selectList: any[] = [];
    const values: any[] = [];
    const { listItems, listSelected } = this.state;
    listItems.map(e => {
      const val = e.value;
      const selectedItem = !!listSelected.find(i => i.value === val);
      selectedItem ? add_selectList.push(e) : new_list.push(e);
    });
    this.setState({
      ...this.state,
      listItems: new_list,
      listSelected: [],
      listSelect: this.state.listSelect.concat(add_selectList)
    });

    new_list.map(l => {
      values.push(l.value);
    });
    this.props.onSelect(values);
  };
  initForm = () => {
    const form = new FormState({
      fieldselect: new FieldState('')
    });
    this.form = form;
  };
  getElevation = (elevation: ElevationType): any => {
    switch (elevation) {
      case 0:
        return Classes.ELEVATION_0;
      case 1:
        return Classes.ELEVATION_1;
      case 2:
        return Classes.ELEVATION_2;
      case 3:
        return Classes.ELEVATION_3;
      case 4:
        return Classes.ELEVATION_4;
      default:
        return Classes.ELEVATION_0;
    }
  };
  headerCustomComponent = (id: string, list: any[]) => {
    return (
      <HeaderVEditableList textcolor={'#fff'}>
        <div style={{ marginRight: 2, marginTop: 15 }}>
          <Tooltip
            usePortal
            hoverCloseDelay={0}
            position={'bottom'}
            content={this.props.toolTipAdd ? this.props.toolTipAdd : 'Add element'}
          >
            <VSelectField
              fieldState={this.form.$.fieldselect}
              id={id.concat(this.props.id)}
              onChange={this.onSlectItem}
              fill
              inline
              minimal
              options={list}
              icon={'plus'}
              iconOnly
            />
          </Tooltip>
        </div>
        <Tooltip
          usePortal
          hoverCloseDelay={0}
          position={'bottom'}
          content={this.props.toolTipAdd ? this.props.toolTipDel : 'Delete element(s)'}
        >
          <Button
            icon="minus"
            minimal
            onClick={() => {
              this.onDeleteItem();
            }}
          />
        </Tooltip>
      </HeaderVEditableList>
    );
  };
  render() {
    const { selection, elevation, className, padding, height, headerText, id, headerColor, headerBgColor } = this.props;
    return (
      <VCardPanel
        headerText={headerText}
        bodyPadding={'1px'}
        headerBackgroundColor={headerBgColor || '#3d3d3d'}
        headerColor={headerColor || '#FFFFFF'}
        headerCustomComponent={this.props.hideBtn ? <></> : this.headerCustomComponent(id, this.state.listSelect)}
      >
        <StyledMenu className={`${this.getElevation(elevation || 0)} ${className}`} padding={padding} height={height}>
          {this.state.listItems.map(element => {
            const active = this.isActive(element);
            const backgroundColor = !!selection && !!selection.background ? selection!.background : '#BFCCD6';
            const textColor = !!selection && !!selection.textColor ? selection!.textColor : '#10161A';
            return (
              <StyledMenuItem
                key={element.value}
                active={active}
                background={backgroundColor}
                color={textColor}
                text={element.label}
                onClick={() => {
                  this.onItemClick(active, element);
                }}
                icon={element.icon}
              />
            );
          })}
        </StyledMenu>
      </VCardPanel>
    );
  }
}
