import { cloneDeep, isEqual, isFinite, size, uniqWith } from 'lodash';

export const onlyNumber = (value: any) => {
  const num = Number(value);
  return !!value && !isFinite(num) && 'Only number';
};
export const required = (val: any) => !val && 'This field is required';
export const uniqueArray = (value: any[]) => {
  const uArray = uniqWith(cloneDeep(value), isEqual);
  return size(uArray) !== size(value) && 'Duplicate';
};
