import React, { Component } from 'react';
import { VCardPanel, VSpinner, VTable } from 'gsi-vx-ui';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { Col, Row } from 'react-flexbox-grid';
import { Button } from '@blueprintjs/core';
import { FCreateOrderContext } from '../search_account/SearchAccount';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { upperCase } from 'lodash';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';

const OrderConsumer = OrderContext.Consumer;

const ThemeConsumer = AppThemeContext.Consumer;
interface IProps extends InjectedIntlProps {
  data: any[];
  isLoading: boolean;
}

class OrderHistory extends Component<IProps> {
  constructor(props: IProps) {
    super(props);
  }

  render() {
    const { data, isLoading } = this.props;
    return (
      <FCreateOrderContext.Consumer>
        {value => (
          <ThemeConsumer>
            {(themeContext: ITheme) =>
              themeContext && (
                <OrderConsumer>
                  {(orderContext: IOrderContext) =>
                    orderContext && (
                      <VCardPanel
                        headerBackgroundColor={themeContext.headers}
                        headerColor={'#FFFFFF'}
                        headerText={this.getNameHeader(0)}
                        bodyPadding={'5px'}
                        headerHorizontalAlign={'center'}
                      >
                        {isLoading ? (
                          <VSpinner />
                        ) : (
                          <VTable
                            columns={[
                              'orderDate',
                              'type',
                              'orderStatus',
                              'qty',
                              'name',
                              'truck',
                              'lastDate',
                              'lastDelQty',
                              'img'
                            ]}
                            cellSelectionType={'ENTIRE_ROW'}
                            columns_name={{
                              orderdate: upperCase(
                                this.props.intl.formatMessage({ id: 'delivery.order.open.order_date' })
                              ),
                              type: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_type' })),
                              orderStatus: upperCase(
                                this.props.intl.formatMessage({ id: 'delivery.order.open.order_status' })
                              ),
                              qty: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_qty' })),
                              truck: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.truck' })),
                              lastDate: upperCase(
                                this.props.intl.formatMessage({ id: 'delivery.order.open.del_date' })
                              ),
                              lastDelQty: upperCase(
                                this.props.intl.formatMessage({ id: 'delivery.order.open.del_qty' })
                              ),
                              img: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.img' })),
                              name: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.driver' }))
                            }}
                            data={data}
                            widgetsCell={[
                              {
                                column: 'type',
                                widget: {
                                  type: 'CUSTOMERCOMPONENT',
                                  cusmtomerCell: { renderCustomer: orderContext.renderCustomerType }
                                }
                              }
                            ]}
                            reordering={true}
                          />
                        )}
                        <Row>
                          <Col xs={12}>
                            <div style={{ display: 'flex', justifyContent: 'center', margin: '10px 0px' }}>
                              <Button
                                icon="disable"
                                text={this.getCancelAndCloseTextFormated()}
                                onClick={value.onCancelOrder}
                              />
                            </div>
                          </Col>
                        </Row>
                      </VCardPanel>
                    )
                  }
                </OrderConsumer>
              )
            }
          </ThemeConsumer>
        )}
      </FCreateOrderContext.Consumer>
    );
  }

  private getCancelAndCloseTextFormated() {
    return `${this.props.intl.formatMessage({ id: 'app.cancel' })} /${this.props.intl.formatMessage({
      id: 'app.close'
    })} `;
  }

  private getNameHeader(result: number) {
    return `${this.props.intl.formatMessage({ id: 'delivery.order.open.order_history' })} (${result})`;
  }
}

export default injectIntl(OrderHistory);
