import React from 'react';
import { onlyNumber } from '../../../commons/Validators';
import { Button } from '@blueprintjs/core';
import { Col } from 'react-flexbox-grid';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { SButtonDown, SCol, styleButton, SVInputField } from '../style';
import { FieldState, FormState } from 'formstate';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';

const OrderConsumer = OrderContext.Consumer;

interface IBottomActions extends InjectedIntlProps {
  isLoading: boolean;
  searchDays: number | string;
}

const BottomActionsComponent: React.FunctionComponent<IBottomActions> = (props: IBottomActions): JSX.Element => {
  const form = new FormState({
    days: new FieldState(props.searchDays)
  });
  return (
    <OrderConsumer>
      {(orderContext: IOrderContext) =>
        orderContext && (
          <React.Fragment>
            <SCol lg={7} xs={12}>
              <SVInputField
                onChange={(data: any) => {
                  orderContext.onSearchByDays(data);
                }}
                type={'text'}
                inline
                validators={[onlyNumber]}
                id={'iddays'}
                label={'Show Pending Open Order with Recent Delivery (Days):'}
                fieldState={form.$.days}
                layer={{ inputWidth: 2, labelWidth: 8 }}
              />
              <SButtonDown icon={'tick'} onClick={orderContext.refresOrder} disabled={props.isLoading}>
                {props.intl.formatMessage({ id: 'delivery.order.open.btn_go' })}
              </SButtonDown>
            </SCol>
            <Col lg={5} xs={12} style={{ display: 'flex', flexDirection: 'row-reverse' }}>
              <Button
                icon={'export'}
                style={{ ...styleButton, marginRight: 5 }}
                onClick={orderContext.onExportExcel}
                disabled={orderContext.isOrdersLoading || orderContext.isOrderMLoading}
              >
                {props.intl.formatMessage({ id: 'delivery.order.review.btn_export_excel' })}
              </Button>
              <Button
                icon={'refresh'}
                style={{ ...styleButton, marginRight: 5 }}
                onClick={orderContext.refreshList}
                disabled={orderContext.isOrdersLoading || orderContext.isOrderMLoading}
              >
                {props.intl.formatMessage({ id: 'delivery.order.open.btn_refresh' })}
              </Button>
            </Col>
          </React.Fragment>
        )
      }
    </OrderConsumer>
  );
};

export default injectIntl(BottomActionsComponent);
