import React, { Component } from 'react';
import { Col, Row } from 'react-flexbox-grid';
import { FieldState, FormState } from 'formstate';
import { observer } from 'mobx-react';
import { Button, Classes, Tooltip } from '@blueprintjs/core';
import { getFormValue, VCardPanel, VInputField, VSelectField } from 'gsi-vx-ui';
import moment from 'moment';
import {
  BtnAddProfile,
  ButtonRowBottom,
  ButtonRowTop,
  DateField,
  FieldsCol,
  FilterCol,
  layerCheck,
  layerNumber,
  NumberField,
  StyledQtyInput,
  StyledTankInput,
  VCardCheckList,
  VCardPanelModList
} from '../../order_management/style';
import { VEditableList } from '../../../../../../commons/components/editable_list/VEditableList';
import { onlyNumber } from '../../../commons/Validators';
import { IPropsFilterForm, IStateFilterForm } from './IFilterForm';
import 'moment/locale/es';
import { injectIntl } from 'react-intl';
import { AppThemeContext, ITheme } from '../../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

@observer
class FilterFormComponent extends Component<IPropsFilterForm, IStateFilterForm> {
  form!: FormState<any>;
  constructor(props: IPropsFilterForm) {
    super(props);
    this.initForm();
  }
  public state: IStateFilterForm = {
    newProfile: false,
    products: [],
    zones: [],
    urgencies: [],
    orderTypes: [],
    endDate: null,
    endOrderQuantity: null,
    startDate: null,
    startOrderQuantity: null,
    startTankPercent: null,
    endTankPercent: null,
    showReturnedOrders: false,
    showUnGeocodeOrders: false,
    excludeCreditHolds: false,
    showOpenOrders: false,
    excludeBalances: false,
    showMarkedOrders: false
  };
  initForm = () => {
    this.form = new FormState({
      startOrderQuantity: new FieldState(this.state.startOrderQuantity).validators(onlyNumber),
      endOrderQuantity: new FieldState(this.state.endOrderQuantity).validators(onlyNumber),
      showReturnedOrders: new FieldState(this.state.showReturnedOrders),
      showMarkedOrders: new FieldState(this.state.showMarkedOrders),
      showOpenOrders: new FieldState(this.state.showOpenOrders),
      startTankPercent: new FieldState(this.state.startTankPercent).validators(onlyNumber),
      endTankPercent: new FieldState(this.state.endTankPercent).validators(onlyNumber),
      excludeBalances: new FieldState(this.state.excludeBalances),
      excludeCreditHolds: new FieldState(this.state.excludeCreditHolds),
      showUnGeocodeOrders: new FieldState(this.state.showUnGeocodeOrders),
      profileId: new FieldState(''),
      startDate: new FieldState(this.state.startDate),
      endDate: new FieldState(this.state.endDate),
      days: new FieldState(''),
      featureName: new FieldState('ORDER_ADVANCED_FILTER'),
      active: new FieldState(false),
      urgencyFactors: new FieldState(this.state.urgencies),
      zones: new FieldState(this.state.zones),
      products: new FieldState(this.state.products),
      orderTypes: new FieldState(this.state.orderTypes),
      name: new FieldState('')
    });
  };
  onSubmit = () => {
    const obj = getFormValue(this.form);
    obj.products = this.form.$.products.value;
    obj.name = this.form.$.name.value;
    obj.zones = this.form.$.zones.value;
    obj.orderTypes = this.form.$.orderTypes.value;
    obj.urgencyFactors = this.form.$.urgencyFactors.value;
    this.form.validate().then(res => {
      !res.hasError && this.props.onSave(obj);
    });
  };
  onSelectProfile = async (idprofile: any) => {
    const profile = this.props.advancedProfiles.find(p => {
      return p.profileId === idprofile;
    });

    const products = await this.getProducts(profile);
    const orderTypes = await this.getOrderTypes(profile);
    const zones = await this.getZones(profile);
    const urgencies = await this.getUrgencies(profile);
    const {
      endDate,
      startDate,
      startOrderQuantity,
      startTankPercent,
      name,
      showReturnedOrders,
      showUnGeocodeOrders,
      excludeCreditHolds,
      showMarkedOrders,
      showOpenOrders,
      excludeBalances,
      endOrderQuantity,
      endTankPercent
    } = profile;
    moment.locale(this.props.intl ? this.props.intl.locale : 'en');
    const newEndDate = endDate && moment(`${endDate[0]}-${endDate[1]}-${endDate[2]}`).toDate();
    const newStartDate = startDate && moment(`${startDate[0]}-${startDate[1]}-${startDate[2]}`).toDate();
    this.form.$.endDate.onChange(newEndDate);
    this.form.$.endOrderQuantity.onChange(endOrderQuantity);
    this.form.$.endTankPercent.onChange(endTankPercent);
    this.form.$.startDate.onChange(newStartDate);
    this.form.$.startOrderQuantity.onChange(startOrderQuantity);
    this.form.$.startTankPercent.onChange(startTankPercent);
    this.form.$.urgencyFactors.onChange(urgencies.idelements);
    this.form.$.zones.onChange(zones.idelements);
    this.form.$.products.onChange(products.idelements);
    this.form.$.orderTypes.onChange(orderTypes.idelements);
    this.form.$.name.onChange(name);
    this.form.$.showReturnedOrders.onChange(showReturnedOrders);
    this.form.$.showUnGeocodeOrders.onChange(showUnGeocodeOrders);
    this.form.$.excludeCreditHolds.onChange(excludeCreditHolds);
    this.form.$.showMarkedOrders.onChange(showMarkedOrders);
    this.form.$.showOpenOrders.onChange(showOpenOrders);
    this.form.$.excludeBalances.onChange(excludeBalances);
    this.changeDays();
    this.setState({
      ...this.state,
      endOrderQuantity,
      endTankPercent,
      startTankPercent,
      startOrderQuantity,
      showReturnedOrders,
      showUnGeocodeOrders,
      excludeCreditHolds,
      showMarkedOrders,
      showOpenOrders,
      excludeBalances,
      startDate: newStartDate,
      endDate: newEndDate,
      products: products.elements,
      orderTypes: orderTypes.elements,
      zones: zones.elements,
      urgencies: urgencies.elements
    });
  };
  onAddCancelProfile = (newProfile: boolean) => {
    this.form.reset();
    this.setState({ ...this.state, newProfile });
    newProfile ? this.form.$.profileId.onChange(-1) : '';
    this.changeDays();
  };
  getProducts = (profile: any) => {
    const elements: any[] = [];
    const idelements: any[] = [];
    profile.products.map((p: any) => {
      elements.push({ label: p.productName, value: p.productId });
      idelements.push(p.productId);
    });
    return { elements, idelements };
  };
  getOrderTypes = (profile: any) => {
    const elements: any[] = [];
    const idelements: any[] = [];
    profile.orderTypes.map((o: any) => {
      elements.push({ label: o.ordTypeName, value: o.orderTypeid });
      idelements.push(o.orderTypeid);
    });
    return { elements, idelements };
  };
  getZones = (profile: any) => {
    const elements: any[] = [];
    const idelements: any[] = [];
    profile.zones.map((z: any) => {
      elements.push({ label: z.zoneName, value: z.zoneId });
      idelements.push(z.zoneId);
    });
    return { elements, idelements };
  };
  getUrgencies = (profile: any) => {
    const elements: any[] = [];
    const idelements: any[] = [];
    profile.urgencyFactors.map((u: any) => {
      elements.push({ label: u.value, value: u.key });
      idelements.push(u.key);
    });
    return { elements, idelements };
  };
  isOnList = (key: any, list: any[]) => {
    return !!list.find((l: any) => l.value === key.value);
  };
  updateList = (listSelected: any[], list: any[]) => {
    const result = !!list.length
      ? listSelected.filter(l => {
          return !this.isOnList(l, list);
        })
      : listSelected;

    return result;
  };
  changeDays = () => {
    const start = moment(this.form.$.startDate.value);
    const end = moment(this.form.$.endDate.value);
    const diff_days = end.diff(start, 'days');
    const days = diff_days && diff_days > 0 ? diff_days : '';
    this.form.$.days.onChange(days);
  };
  getProfileInput = () => {
    const { listProfiles, isLoading } = this.props;
    return this.state.newProfile ? (
      <VInputField
        inline
        layer={{ inputOrientation: 'end', inputWidth: 10 }}
        label={'Profile'}
        id={'idnewprofile'}
        fieldState={this.form.$.name}
        fill
        required
      />
    ) : (
      <VSelectField
        label={'Profile'}
        id={'idselectprofile'}
        inline
        required
        className={isLoading ? Classes.SKELETON : ''}
        onChange={this.onSelectProfile}
        fieldState={this.form.$.profileId}
        placeholder={'Select Profile'}
        layer={{ inputOrientation: 'end', inputWidth: 12 }}
        fill
        options={listProfiles}
      />
    );
  };
  getEditableListInputs = () => {
    const { productList, zoneslist, urgencylist, typeslist } = this.props;
    const { urgencies, products, zones, orderTypes } = this.state;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <Row>
              <FilterCol xs={12} sm={12} md={6} lg={3}>
                <VEditableList
                  headerText={'Product'}
                  elementsSelected={products}
                  toolTipAdd={'Add product'}
                  headerBgColor={themeContext.headers}
                  headerColor={themeContext.sidebar_text}
                  toolTipDel={'Delete product'}
                  listSelectElements={this.updateList(productList, products)}
                  id={'ideditproduct'}
                  onSelect={(data: any) => {
                    this.form.$.products.onChange(data);
                  }}
                />
              </FilterCol>
              <FilterCol xs={12} sm={12} md={6} lg={3}>
                <VEditableList
                  headerText={'Order Type'}
                  toolTipAdd={'Add type'}
                  toolTipDel={'Delete type'}
                  headerBgColor={themeContext.headers}
                  headerColor={themeContext.sidebar_text}
                  elementsSelected={orderTypes}
                  listSelectElements={typeslist}
                  id={'ideditorder'}
                  onSelect={(data: any) => {
                    this.form.$.orderTypes.onChange(data);
                  }}
                />
              </FilterCol>
              <FilterCol xs={12} sm={12} md={6} lg={3}>
                <VEditableList
                  headerText={'Route'}
                  elementsSelected={zones}
                  toolTipAdd={'Add route'}
                  toolTipDel={'Delete route'}
                  headerBgColor={themeContext.headers}
                  headerColor={themeContext.sidebar_text}
                  listSelectElements={zoneslist}
                  id={'ideditrout'}
                  onSelect={(data: any) => {
                    this.form.$.zones.onChange(data);
                  }}
                />
              </FilterCol>
              <FilterCol xs={12} sm={12} md={6} lg={3}>
                <VEditableList
                  headerText={'Urgency'}
                  toolTipAdd={'Add urgency'}
                  headerBgColor={themeContext.headers}
                  headerColor={themeContext.sidebar_text}
                  toolTipDel={'Delete urgency'}
                  id={'idediturgency'}
                  elementsSelected={urgencies}
                  listSelectElements={urgencylist}
                  onSelect={(data: any) => {
                    this.form.$.urgencyFactors.onChange(data);
                  }}
                />
              </FilterCol>
            </Row>
          )
        }
      </ThemeConsumer>
    );
  };
  getTextCheckFields = () => {
    const form = this.form;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <Row>
              <FilterCol xs={12} sm={12} md={6} lg={3}>
                <VCardPanel
                  headerText={'Order Quantity'}
                  bodyPadding={'1px'}
                  headerBackgroundColor={themeContext.headers}
                  headerColor={'#FFFFFF'}
                >
                  <StyledQtyInput
                    id={'quantity>='}
                    inline
                    fieldState={form.$.startOrderQuantity}
                    label={'Quantity >='}
                    layer={layerNumber}
                  />
                  <StyledQtyInput
                    id={'quantity<='}
                    inline
                    fieldState={form.$.endOrderQuantity}
                    label={'Quantity <='}
                    layer={layerNumber}
                  />
                </VCardPanel>
              </FilterCol>
              <FilterCol xs={12} sm={12} md={6} lg={3}>
                <VCardPanel
                  headerText={'Tank Percentaje'}
                  bodyPadding={'1px'}
                  headerBackgroundColor={themeContext.headers}
                  headerColor={'#FFFFFF'}
                >
                  <StyledTankInput
                    id={'pct>='}
                    inline
                    fieldState={form.$.startTankPercent}
                    label={'Pct >='}
                    layer={{
                      inputWidth: 9
                    }}
                  />
                  <StyledTankInput
                    id={'pct<='}
                    inline
                    fieldState={form.$.endTankPercent}
                    label={'Pct <='}
                    layer={{
                      inputWidth: 9
                    }}
                  />
                </VCardPanel>
              </FilterCol>
              <FilterCol xs={12} sm={12} lg={6}>
                <VCardPanelModList
                  headerText={'Open Status'}
                  bodyPadding={'1px'}
                  headerBackgroundColor={themeContext.headers}
                  headerColor={'#FFFFFF'}
                >
                  <Row>
                    <Col xs={6}>
                      <VCardCheckList
                        alignIndicator={'right'}
                        label="Returned Orders Only"
                        inline
                        checkBoxAtLeft
                        layer={layerCheck}
                        fieldState={form.$.showReturnedOrders}
                        id={'1'}
                      />
                    </Col>
                    <Col xs={6}>
                      <VCardCheckList
                        alignIndicator={'right'}
                        checkBoxAtLeft
                        layer={layerCheck}
                        label="Marked Orders Only"
                        inline
                        id={'2'}
                        fieldState={form.$.showMarkedOrders}
                      />
                    </Col>
                  </Row>
                  <Row>
                    <Col xs={6}>
                      <VCardCheckList
                        alignIndicator={'right'}
                        checkBoxAtLeft
                        layer={layerCheck}
                        label="All Open Orders"
                        inline
                        fieldState={form.$.showOpenOrders}
                        id={'3'}
                      />
                    </Col>
                    <Col xs={6}>
                      <VCardCheckList
                        alignIndicator={'right'}
                        checkBoxAtLeft
                        layer={layerCheck}
                        label="Exclude Balances"
                        inline
                        fieldState={form.$.excludeBalances}
                        id={'4'}
                      />
                    </Col>
                  </Row>
                  <Row>
                    <Col xs={6}>
                      <VCardCheckList
                        alignIndicator={'right'}
                        checkBoxAtLeft
                        layer={layerCheck}
                        label="Exclude Credit Hold"
                        inline
                        fieldState={form.$.excludeCreditHolds}
                        id={'5'}
                      />
                    </Col>
                    <Col xs={6}>
                      <VCardCheckList
                        alignIndicator={'right'}
                        checkBoxAtLeft
                        layer={layerCheck}
                        label="Ungeocoded Only"
                        inline
                        fieldState={form.$.showUnGeocodeOrders}
                        id={'6'}
                      />
                    </Col>
                  </Row>
                </VCardPanelModList>
              </FilterCol>
            </Row>
          )
        }
      </ThemeConsumer>
    );
  };
  getDateInputFields = () => {
    return (
      <FieldsCol xs={12} lg={10}>
        <DateField
          dateType={'DATE'}
          layer={{ inputWidth: 6 }}
          id={'iddialstartorderdate'}
          label={'Start Order Date'}
          onChange={this.changeDays}
          inline
          fieldState={this.form.$.startDate}
        />
        <DateField
          dateType={'DATE'}
          layer={{ inputWidth: 6 }}
          inline
          onChange={this.changeDays}
          id={'iddialendorderdate'}
          label={'End Order Date'}
          fieldState={this.form.$.endDate}
        />
        <NumberField
          layer={{ inputWidth: 4 }}
          inline
          type={'text'}
          disabled
          id={'iddays'}
          label={'Days'}
          fieldState={this.form.$.days}
        />
      </FieldsCol>
    );
  };
  render() {
    return (
      <div style={{ width: '100%', padding: '0 20px' }}>
        <ButtonRowTop>
          <FilterCol>
            <div style={{ width: 210 }}>{this.getProfileInput()}</div>
          </FilterCol>
          {this.state.newProfile ? (
            <Tooltip usePortal hoverCloseDelay={0} position={'bottom'} content={'Cancel'}>
              <BtnAddProfile icon="disable" onClick={() => this.onAddCancelProfile(false)} />
            </Tooltip>
          ) : (
            <Tooltip usePortal hoverCloseDelay={0} position={'bottom'} content={'Add profile'}>
              <BtnAddProfile icon="plus" onClick={() => this.onAddCancelProfile(true)} />
            </Tooltip>
          )}
        </ButtonRowTop>
        {this.getEditableListInputs()}
        {this.getTextCheckFields()}
        <Row>{this.getDateInputFields()}</Row>
        <ButtonRowBottom>
          <Col>
            <Button icon="saved" text="Save" large onClick={this.onSubmit} />
          </Col>
        </ButtonRowBottom>
      </div>
    );
  }
}
export default injectIntl(FilterFormComponent);
