import React, { Component } from 'react';
import { AnchorButton, Tooltip } from '@blueprintjs/core';
import styled from 'styled-components';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { Col, Row } from 'react-flexbox-grid';
import { FFilterAccContext } from '../../SearchAccount';

const WhiteButton = styled(AnchorButton)`
  svg {
    fill: #fff !important;
  }
`;

class SearchCriteriaActions extends Component<InjectedIntlProps> {
  render() {
    const { intl } = this.props;
    return (
      <FFilterAccContext.Consumer>
        {value => (
          <Row end={'xs'}>
            <Col xs={6}>
              <Tooltip
                usePortal
                hoverCloseDelay={0}
                position={'bottom'}
                content={intl.formatMessage({
                  id: 'commons.search',
                  defaultMessage: 'Search'
                })}
              >
                <WhiteButton onClick={value.onSearchAcc} icon={'search'} fill minimal disabled={value.loadingAccount} />
              </Tooltip>
            </Col>
            <Col xs={6}>
              <Tooltip
                usePortal
                hoverCloseDelay={0}
                position={'bottom'}
                content={intl.formatMessage({
                  id: 'commons.clear',
                  defaultMessage: 'Clear'
                })}
              >
                <WhiteButton
                  onClick={value.onClearSearchAcc}
                  icon={'eraser'}
                  fill
                  minimal
                  disabled={value.loadingAccount}
                />
              </Tooltip>
            </Col>
          </Row>
        )}
      </FFilterAccContext.Consumer>
    );
  }
}

export default injectIntl(SearchCriteriaActions);
