import styled from 'styled-components';
import { Col, Row } from 'react-flexbox-grid';
import { VCardPanel, VCheckboxField, VDateTimePicker, VInputField } from 'gsi-vx-ui';
import { Button } from '@blueprintjs/core';
import { VEditableList } from '../../../../../commons/components/editable_list/VEditableList';
import { ILayer } from 'gsi-vx-ui/lib/types/Form/Inputs/ILayer';

export const layerCheck: ILayer = {
  labelWidth: 12,
  inputWidth: 1
};
export const layerNumber: ILayer = {
  inputWidth: 7
};

export const FieldsCol = styled(Col)`
  display: flex;
  padding-bottom: 5px;
`;
export const SearchCol = styled(FieldsCol)`
  justify-content: flex-end;
`;
export const DateField = styled(VDateTimePicker)`
  padding-left: 2px;
`;

export const NumberField = styled(VInputField)`
  padding-left: 2px;
`;
export const DateFields = styled(FieldsCol)`
  flex-direction: column;
`;
export const BtnAddProfile = styled(Button)`
  height: 30px;
  margin-left: 5px;
`;
export const NoBorderList = styled(VEditableList)`
  ul {
    border: none;
    padding: 0;
    box-shadow: none;
    margin: -10px;
  }
`;

export const VCardPanelModList = styled(VCardPanel)`
  padding-left: 3px;
`;
export const VCardCheckList = styled(VCheckboxField)`
  padding-left: 0px;
  & label {
    margin-left: 14px !important;
  }
`;

export const StyledDateInput = styled(VInputField)`
  & div {
    width: 100%;
    padding-left: 5px;
    padding-top: 3px;
  }
  & label {
    padding-top: 3px;
    min-width: 65px;
  }
`;
export const StyledQtyInput = styled(VInputField)`
  & div {
    width: 100%;
    padding-left: 1px;
    padding-top: 3px;
  }
  & label {
    padding-top: 1px;
    padding-left: 4px;
    min-width: 80px;
  }
`;
export const StyledTankInput = styled(VInputField)`
  & div {
    width: 100%;
    padding-left: 1px;
    padding-top: 3px;
  }
  & label {
    padding-top: 1px;
    padding-left: 4px;
    min-width: 20px;
  }
  & input {
    width: 175px !important;
  }
`;

export const FilterCol = styled(Col)`
  margin-bottom: 20px;
`;

export const ButtonRow = styled(Row)`
  padding: 8px;
`;
export const ButtonRowBottom = styled(ButtonRow)`
  justify-content: flex-end;
`;
export const ButtonRowTop = styled(ButtonRow)`
  height: 46px;
  margin: 10px 0;
`;
export const WhiteButton = styled(Button)`
  color: white !important;
  & > span {
    color: white !important;
  }
`;
export const BlackButton = styled(Button)`
  color: black !important;
  & > span {
    color: black !important;
  }
`;
