import React, { Component } from 'react';
import { VTable } from 'gsi-vx-ui';
import { IListTable } from '../../../type/OrderManagementType';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';
import { injectIntl } from 'react-intl';
import { upperCase } from 'lodash';

const OrderConsumer = OrderContext.Consumer;

class ListDuplicateComponent extends Component<IListTable> {
  constructor(props: IListTable) {
    super(props);
  }
  render() {
    const { orders, onRowClick } = this.props;
    return (
      <OrderConsumer>
        {(orderContext: IOrderContext) =>
          orderContext && (
            <VTable
              cellSelectionType={'ENTIRE_ROW'}
              striped
              columns={this.props.columns}
              enableColumnResizing={true}
              actionsSelection={{ onSelectionChange: onRowClick }}
              typeHeightRow={'HALF'}
              columnWidths={[120, 120, 120, 125]}
              widgetsCell={orderContext.renderCustom(this.props.columns)}
              columns_name={{
                accountId: this.props.intl.formatMessage({ id: 'delivery.order.review.cmAccId' }),
                orderId: this.props.intl.formatMessage({ id: 'delivery.order.review.cmOrdId' }),
                orderStatus: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_status' })),
                colortype: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_type' })),
                orderDate: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_date' })),
                customerName: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.name' })),
                productName: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.product' })),
                qty: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.order_qty' })),
                lastDate: this.props.intl.formatMessage({ id: 'delivery.order.review.cmLastDate' }),
                lastDelQty: this.props.intl.formatMessage({ id: 'delivery.order.review.cmLastQty' }),
                lastDelDriver: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.review.cmLastDriver' }))
              }}
              data={orders}
            />
          )
        }
      </OrderConsumer>
    );
  }
}

export default injectIntl(ListDuplicateComponent);
