import React from 'react';
import OpenOrdersComponent from '../open/OpenOrdersComponent';
import IncompleteComponent from '../incomplete/IncompleteComponent';
import DuplicateComponent from '../duplicate/DuplicateComponent';
import UnGeoOrdersComponent from '../ungeo_orders/UnGeoOrdersComponent';

interface IListOrders {
  total: number;
  filterByUrgency: boolean;
  searchDays: number | string;
  activeList: string;
}

const ListOrdersComponent: React.FunctionComponent<IListOrders> = (props: IListOrders): JSX.Element => {
  const { activeList, filterByUrgency, searchDays } = props;
  switch (activeList) {
    case 'open':
    case 'total':
      return <OpenOrdersComponent filter={filterByUrgency} days={searchDays} />;
      break;
    case 'incomplete':
      return <IncompleteComponent filter={filterByUrgency} days={searchDays} />;
    case 'duplicate':
      return <DuplicateComponent filter={filterByUrgency} days={searchDays} />;
      break;
    case 'ungeo':
      return <UnGeoOrdersComponent filter={filterByUrgency} days={searchDays} />;
      break;
    default:
      return <></>;
  }
};

export default ListOrdersComponent;
