import React, { Component } from 'react';
import { VCardPanel, VSpinner, VTable } from 'gsi-vx-ui';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { FieldState, FormState } from 'formstate';
import { SVCheckboxField } from '../style';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { upperCase } from 'lodash';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';
import { inject, observer } from 'mobx-react';
import { UrgencySummaryStore } from '../../../stores/UrgencySummaryStore';
import { CellSelectionType } from 'gsi-vx-ui/lib/types/Table/type';

const OrderConsumer = OrderContext.Consumer;

const ThemeConsumer = AppThemeContext.Consumer;

interface IListProps extends InjectedIntlProps {
  urgency_summary_store?: UrgencySummaryStore;
  selType: CellSelectionType;
  onChangeFilter?(data: any): void;
}
interface IListState {
  clearSelection: boolean;
}

@inject('urgency_summary_store')
@observer
class ListDeliveryTypeComponent extends Component<IListProps, IListState> {
  form!: FormState<any>;
  constructor(props: IListProps) {
    super(props);
    this.initForm();
  }
  initForm = () => {
    const form = new FormState({
      select_profile: new FieldState(''),
      chkFilter: new FieldState(true),
      chkUngeocodedOnly: new FieldState(''),
      fSearch: new FieldState(''),
      startOrderDate: new FieldState(''),
      endOrderDate: new FieldState(''),
      days: new FieldState('')
    });
    this.form = form;
  };
  headerCustomComponent = (disable: boolean) => {
    return (
      <div style={{ marginRight: 20, marginTop: 15 }}>
        <OrderConsumer>
          {(orderContext: IOrderContext) =>
            orderContext && (
              <SVCheckboxField
                id="idchekfield"
                label={' Filter'}
                alignIndicator={'right'}
                inline
                disabled={disable}
                fieldState={this.form.$.chkFilter}
                onChange={this.props.onChangeFilter}
              />
            )
          }
        </OrderConsumer>
      </div>
    );
  };
  public state: IListState = {
    clearSelection: false
  };
  render() {
    const { intl, urgency_summary_store, selType } = this.props;
    const { isLoading } = urgency_summary_store!;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <OrderConsumer>
              {(orderContext: IOrderContext) =>
                orderContext && (
                  <VCardPanel
                    headerText={`${intl.formatMessage({ id: 'delivery.order.open.title_list_urgency' })}`}
                    bodyPadding={'0px'}
                    headerTextJustify={'center'}
                    headerBackgroundColor={themeContext.headers}
                    headerColor={'#FFFFFF'}
                    headerCustomComponent={this.headerCustomComponent(orderContext.isOrdersLoading)}
                  >
                    {isLoading ? (
                      <VSpinner />
                    ) : (
                      <VTable
                        columns={[
                          'orderType',
                          'incomplete',
                          'duplicate',
                          'unGeo',
                          'urgency1',
                          'urgency2',
                          'urgency3',
                          'urgency4',
                          'urgency5',
                          'total'
                        ]}
                        data={urgency_summary_store!.elements}
                        cellSelectionType={selType}
                        actionsSelection={{
                          clearSelection: this.state.clearSelection,
                          onSelectionChange: orderContext.listTypeClick
                        }}
                        typeHeightRow={'HALF'}
                        configColumnsHeader={[
                          { column: 'urgency1', textColor: '#FFFFFF', backgroundColor: '#1b242a' },
                          { column: 'urgency2', textColor: '#FFFFFF', backgroundColor: '#f24444' },
                          { column: 'urgency3', textColor: '#FFFFFF', backgroundColor: '#ffd307' },
                          { column: 'urgency4', textColor: '#FFFFFF', backgroundColor: '#20a8a4' },
                          { column: 'urgency5', textColor: '#FFFFFF', backgroundColor: '#4bacef' }
                        ]}
                        columnWidths={[140, 120, 120]}
                        widgetsCell={[
                          {
                            column: 'orderType',
                            widget: {
                              type: 'CUSTOMERCOMPONENT',
                              cusmtomerCell: { renderCustomer: orderContext.renderDeliveryType }
                            }
                          }
                        ]}
                        columns_name={{
                          orderType: upperCase(intl.formatMessage({ id: 'delivery.order.open.delivery_type' })),
                          unGeo: 'UNGEO',
                          incomplete: 'INCOMPLETE',
                          duplicate: 'DUPLICATES',
                          urgency1: 'URGE (1)',
                          urgency2: 'URGE (2)',
                          urgency3: 'URGE (3)',
                          urgency4: 'URGE (4)',
                          total: 'TOTAL',
                          urgency5: 'URGE (5)'
                        }}
                      />
                    )}
                  </VCardPanel>
                )
              }
            </OrderConsumer>
          )
        }
      </ThemeConsumer>
    );
  }
}
export default injectIntl(ListDeliveryTypeComponent);
