import React, { Component } from 'react';
import { Col, Row } from 'react-flexbox-grid';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import SearchCriteriaAccountForm from './components/search_criteria_account_form/SearchCriteriaAccountForm';
import { FieldState, FormState } from 'formstate';
import SearchResults from '../search_results/SearchResults';
import OrderHistory from '../order_history/OrderHistory';
import { inject, observer } from 'mobx-react';
import { OrderAccountStore } from '../../../stores/OrderAccountStore';
import { OrderHistoryStore } from '../../../stores/OrderHistoryStore';
import { OrderManagementStore } from '../../../stores/OrderManagementStore';
import { VCardPanel } from 'gsi-vx-ui';
import OrderDetailsComponent from '../order_details/OrderDetailsComponent';
import { Dialog } from '@blueprintjs/core';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { OrdersStore } from '../../../stores/OrdersStore';
import CustomComponent from '../../../commons/CustomComponent';

const ThemeConsumer = AppThemeContext.Consumer;

interface ISearchAccount extends InjectedIntlProps {
  onSaveOrder(): void;
  onClose(): void;
  order_management_store?: OrderManagementStore;
  accounts_orders_store?: OrderAccountStore;
  history_orders_store?: OrderHistoryStore;
  orders_store?: OrdersStore;
}
export const FFilterAccContext = React.createContext({
  onSearchAcc: () => {},
  onClearSearchAcc: () => {},
  loadingAccount: false
});
export const FCreateOrderContext = React.createContext({
  onCancelOrder: () => {}
});
@inject('order_management_store', 'accounts_orders_store', 'history_orders_store', 'orders_store')
@observer
class SearchAccount extends Component<ISearchAccount> {
  form: FormState<any>;

  constructor(props: ISearchAccount) {
    super(props);
    this.form = new FormState<any>({
      accountId: new FieldState(''),
      name: new FieldState(''),
      productId: new FieldState(''),
      city: new FieldState(''),
      stateProv: new FieldState('')
    });
    this.props.accounts_orders_store!.getElements();
  }
  onSearchAccount = () => {
    const { accounts_orders_store } = this.props;
    this.form.$.accountId.value &&
      accounts_orders_store!.addFilter({ field: 'accountId', value: this.form.$.accountId.value, operator: 'LIKE' });
    this.form.$.productId.value &&
      accounts_orders_store!.addFilter({
        field: 'productId',
        value: this.form.$.productId.value,
        operator: 'EQUALITY'
      });
    this.form.$.city.value &&
      accounts_orders_store!.addFilter({ field: 'city', value: this.form.$.city.value, operator: 'LIKE' });
    this.form.$.stateProv.value &&
      accounts_orders_store!.addFilter({ field: 'stateId', value: this.form.$.stateProv.value, operator: 'EQUALITY' });
    this.form.$.name.value &&
      accounts_orders_store!.addFilter({ field: 'name', value: this.form.$.name.value, operator: 'LIKE' });
    this.props.accounts_orders_store!.getElements();
  };
  onEraserSearch = () => {
    const { accounts_orders_store } = this.props;
    accounts_orders_store!.removeFilter({ field: 'accountId', value: this.form.$.accountId.value, operator: 'LIKE' });
    accounts_orders_store!.removeFilter({
      field: 'productId',
      value: this.form.$.productId.value,
      operator: 'EQUALITY'
    });
    accounts_orders_store!.removeFilter({ field: 'city', value: this.form.$.city.value, operator: 'LIKE' });
    accounts_orders_store!.removeFilter({ field: 'stateId', value: this.form.$.stateProv.value, operator: 'EQUALITY' });
    this.form.reset();
    this.props.accounts_orders_store!.getElements();
  };
  onSelectAcct = (data: any) => {
    const { accounts_orders_store, history_orders_store } = this.props;
    accounts_orders_store!.setElement(data);
    history_orders_store!.addFilter({ field: 'acctId', value: data.acctId, operator: 'EQUALITY' });
    history_orders_store!.getElements();
  };
  handleSaveDetails = () => {
    const { accounts_orders_store } = this.props;
    accounts_orders_store!.setOpenDetails(false);
    this.props.onSaveOrder();
  };
  onCreateOrder = () => {
    this.props.accounts_orders_store!.setOpenDetails(true);
  };
  onReorderAcc = (columns: string[]) => {
    this.props.accounts_orders_store!.setColumnsAcc(columns);
  };
  componentWillUnmount() {
    this.props.accounts_orders_store!.resetElement();
    this.props.history_orders_store!.removeFilter({ field: 'acctId', value: '', operator: 'EQUALITY' });
  }
  onCloseDetails() {
    this.props.accounts_orders_store!.setOpenDetails(false);
  }
  getOrderDetailsComponent = () => {
    const { accounts_orders_store } = this.props;
    return (
      <Dialog
        isOpen={true}
        canOutsideClickClose={false}
        onClose={() => accounts_orders_store!.setOpenDetails(false)}
        style={{ width: '85%', paddingBottom: '0' }}
        canEscapeKeyClose={false}
      >
        <ThemeConsumer>
          {(themeContext: ITheme) =>
            themeContext && (
              <VCardPanel
                headerText={this.props.intl.formatMessage({ id: 'delivery.order.open.titleDetails' })}
                bodyPadding={'1px'}
                headerBackgroundColor={themeContext.headers}
                headerColor={'#FFFFFF'}
                headerTextJustify={'center'}
                headerCustomComponent={<CustomComponent action={this.onCloseDetails.bind(this)} />}
              >
                <OrderDetailsComponent
                  orderDetails={accounts_orders_store!.element}
                  onSave={this.handleSaveDetails}
                  onCancel={() => accounts_orders_store!.setOpenDetails(false)}
                  nofees={0}
                />
              </VCardPanel>
            )
          }
        </ThemeConsumer>
      </Dialog>
    );
  };
  render() {
    const { order_management_store, accounts_orders_store } = this.props;
    const { listStates, listProducts } = order_management_store!;
    const { elements, isLoading, count, element, openDetails } = accounts_orders_store!;
    return (
      <>
        <Row>
          <Col xs={12} lg={3}>
            <FFilterAccContext.Provider
              value={{
                onSearchAcc: this.onSearchAccount,
                onClearSearchAcc: this.onEraserSearch,
                loadingAccount: isLoading
              }}
            >
              <SearchCriteriaAccountForm form={this.form} products={listProducts} states={listStates} />
            </FFilterAccContext.Provider>
          </Col>
          <Col xs={12} lg={9}>
            <SearchResults
              data={elements}
              columns={accounts_orders_store!.columnsAcc}
              onSelectAcct={this.onSelectAcct}
              onOrderColumns={this.onReorderAcc}
              onCreateOrder={this.onCreateOrder}
              disableBtn={!(element && element.acctId)}
              isLoading={isLoading}
              numberAccount={count}
            />
          </Col>
        </Row>
        <br />
        <Row>
          <Col xs={12}>
            <FCreateOrderContext.Provider value={{ onCancelOrder: this.props.onClose }}>
              <OrderHistory
                isLoading={this.props.history_orders_store!.isLoading}
                data={this.props.history_orders_store!.elements}
              />
            </FCreateOrderContext.Provider>
          </Col>
          {openDetails && this.getOrderDetailsComponent()}
        </Row>
      </>
    );
  }
}

export default injectIntl(SearchAccount);
