import React, { Component } from 'react';
import { disposeOnUnmount, inject, observer } from 'mobx-react';
import { VCardPanel, VSpinner } from 'gsi-vx-ui';
import { Dialog } from '@blueprintjs/core';
import ListIncompleteComponent from './ListIncompleteComponent';
import { IncompleteStore } from '../../../stores/IncompleteStore';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import OrderDetailsComponent from '../order_details/OrderDetailsComponent';
import CustomComponent from '../../../commons/CustomComponent';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';
import { reaction } from 'mobx';

const OrderConsumer = OrderContext.Consumer;

const ThemeConsumer = AppThemeContext.Consumer;
interface IIncompleteComponent extends InjectedIntlProps {
  incomplete_orders_store?: IncompleteStore;
  days: number | string;
  filter: boolean;
}

@inject('incomplete_orders_store')
@observer
class IncompleteComponent extends Component<IIncompleteComponent> {
  constructor(props: IIncompleteComponent) {
    super(props);
    this.onChangeFilter();
    this.onChangeDays();
    disposeOnUnmount(this, [
      reaction(
        () => this.props.filter,
        () => {
          this.onChangeFilter();
        }
      ),
      reaction(
        () => this.props.days,
        () => {
          this.onChangeDays();
        }
      )
    ]);
  }
  onChangeDays = () => {
    this.props.days
      ? this.props.incomplete_orders_store!.addFilter({
          value: this.props.days,
          field: 'numberDate',
          operator: 'EQUALITY'
        })
      : this.props.incomplete_orders_store!.removeFilter({
          value: '',
          field: 'numberDate',
          operator: 'EQUALITY'
        });
  };
  onSelectOrder = (data: any) => {
    data ? this.props.incomplete_orders_store!.setElement(data) : this.props.incomplete_orders_store!.resetElement();
  };
  componentWillUnmount() {
    this.props.incomplete_orders_store!.resetElement();
    this.props.incomplete_orders_store!.setOpenDetails(false);
  }
  handleSaveDetails = () => {
    const { incomplete_orders_store } = this.props;
    incomplete_orders_store!.setOpenDetails(false);
    incomplete_orders_store!.getElements();
  };
  onChangeFilter = () => {
    this.props.filter && this.props.incomplete_orders_store!.updateFilterType();
    this.props.incomplete_orders_store!.setFilterBySummary(this.props.filter);
  };
  render() {
    const { incomplete_orders_store } = this.props;
    const { elements, count, isLoading, element, openDetails } = incomplete_orders_store!;
    return (
      <div style={{ margin: 5 }}>
        <ThemeConsumer>
          {(themeContext: ITheme) =>
            themeContext && (
              <OrderConsumer>
                {(orderContext: IOrderContext) =>
                  orderContext && (
                    <VCardPanel
                      headerText={`Incomplete Orders (${count} of ${orderContext.totalOrders})`}
                      bodyPadding={'1px'}
                      headerTextJustify={'center'}
                      headerBackgroundColor={themeContext.headers}
                      headerColor={'#FFFFFF'}
                    >
                      {isLoading ? (
                        <VSpinner />
                      ) : (
                        <ListIncompleteComponent
                          orders={elements}
                          columns={['orderId', 'type', 'error', 'accountId', 'name', 'lastDate', 'orderDate', 'qty']}
                          onRowClick={this.onSelectOrder}
                          renderCustomerType={orderContext.renderCustomerType}
                        />
                      )}
                    </VCardPanel>
                  )
                }
              </OrderConsumer>
            )
          }
        </ThemeConsumer>
        {openDetails && (
          <Dialog
            isOpen={true}
            canOutsideClickClose={false}
            onClose={() => incomplete_orders_store!.setOpenDetails(false)}
            style={{ width: '85%', paddingBottom: '0' }}
            canEscapeKeyClose={false}
          >
            <ThemeConsumer>
              {(themeContext: ITheme) =>
                themeContext && (
                  <VCardPanel
                    headerText={this.props.intl.formatMessage({ id: 'delivery.order.open.titleDetails' })}
                    bodyPadding={'1px'}
                    headerBackgroundColor={themeContext.headers}
                    headerColor={'#FFFFFF'}
                    headerTextJustify={'center'}
                    headerCustomComponent={
                      <CustomComponent action={() => incomplete_orders_store!.setOpenDetails(false)} />
                    }
                  >
                    <OrderDetailsComponent
                      orderDetails={incomplete_orders_store!.element}
                      onSave={this.handleSaveDetails}
                      onCancel={() => incomplete_orders_store!.setOpenDetails(false)}
                      nofees={0}
                    />
                  </VCardPanel>
                )
              }
            </ThemeConsumer>
          </Dialog>
        )}
      </div>
    );
  }
}

export default injectIntl(IncompleteComponent);
