import React, { Component } from 'react';
import { showToastNotification, VCardPanel, VSpinner, VTable } from 'gsi-vx-ui';
import OrderDetailsFeesActions from '../order_details_fees_actions/OrderDetailsFeesActions';
import { AppThemeContext, ITheme } from '../../../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

const fees: any[] = [];
const datafees: any[] = [];
export const FOrderFeeContext = React.createContext({
  onSelectFee: (e: any) => {},
  onDelFee: (e: any) => {},
  listFees: fees,
  dataFee: fees,
  isLoading: false
});

const styleDiv = { display: 'flex', alignItems: 'center', justifyContent: 'center' };
interface IDetailsFees {
  element: any;
}
class OrderDetailsFees extends Component<{}, IDetailsFees> {
  public state: IDetailsFees = {
    element: null
  };

  render() {
    return (
      <FOrderFeeContext.Consumer>
        {value =>
          value.isLoading ? (
            <VSpinner />
          ) : (
            <ThemeConsumer>
              {(themeContext: ITheme) =>
                themeContext && (
                  <VCardPanel
                    headerTextJustify={'center'}
                    headerText={'Aditional Fees'}
                    headerBackgroundColor={themeContext.headers}
                    headerColor={'#FFFFFF'}
                    height={'275px'}
                    bodyPadding={'0'}
                    headerCustomComponent={<OrderDetailsFeesActions element={this.state.element} />}
                  >
                    <VTable
                      tableHeight={'206px'}
                      columns={['description', 'fee', 'overrideFee']}
                      data={value.dataFee || []}
                      reordering
                      typeHeightRow={'HALF'}
                      columns_name={{
                        description: 'Description',
                        overrideFee: 'Overview Fee'
                      }}
                      cellSelectionType={'ENTIRE_ROW'}
                      edit={{
                        editColumn: {
                          columns: ['fee', 'overrideFee']
                        },
                        onSave: (data: any) => {},
                        invalidDataMessage: (invalidColumns: string[]) => {
                          showToastNotification({
                            type: 'danger',
                            message: `No se puede guardar porq las siguiente columnas tienen datos invalidos: ${invalidColumns.join(
                              ' , '
                            )}`,
                            timeout: 8000
                          });
                        },

                        editToolbar: {
                          textSave: 'Finish',
                          textCancel: 'Cancel',
                          iconCancel: 'cross',
                          iconEdit: 'edit',
                          iconSave: 'share'
                        }
                      }}
                      actionsSelection={{
                        onSelectionChange: (data: any) => {
                          this.setState({ ...this.state, element: data });
                        }
                      }}
                    />
                  </VCardPanel>
                )
              }
            </ThemeConsumer>
          )
        }
      </FOrderFeeContext.Consumer>
    );
  }
}

export default OrderDetailsFees;
