import React, { Component } from 'react';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { VCardPanel, VSpinner, VTable } from 'gsi-vx-ui';
import { Col, Row } from 'react-flexbox-grid';
import { Button } from '@blueprintjs/core';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { upperCase } from 'lodash';

const ThemeConsumer = AppThemeContext.Consumer;
interface IProps extends InjectedIntlProps {
  data: any[];
  columns: any[];
  isLoading: boolean;
  disableBtn?: boolean;
  numberAccount: number;
  onOrderColumns(columns: string[]): void;
  onSelectAcct(data: any): void;
  onCreateOrder(): void;
}

interface IState {}

class SearchResults extends Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);
  }

  render() {
    const nameHeader = this.getNameHeader(this.props.numberAccount);
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              headerText={nameHeader}
              bodyPadding={'5px'}
              headerHorizontalAlign={'center'}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#FFFFFF'}
            >
              {this.props.isLoading ? (
                <VSpinner />
              ) : (
                <VTable
                  tableHeight={'220px'}
                  columns={this.props.columns}
                  cellSelectionType={'ENTIRE_ROW'}
                  onOrderColumns={this.props.onOrderColumns}
                  actionsSelection={{ onSelectionChange: this.props.onSelectAcct }}
                  columns_name={{
                    accountId: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.account' })),
                    name: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.name' })),
                    fullAddress: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.address' })),
                    city: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.city' })),
                    state: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.state_prov' })),
                    productName: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.product' })),
                    tanksize: upperCase(this.props.intl.formatMessage({ id: 'delivery.order.open.tank' }))
                  }}
                  data={this.props.data}
                  reordering={true}
                />
              )}

              <Row>
                <Col xs={12}>
                  <div style={{ display: 'flex', justifyContent: 'flex-end', margin: '10px 0px' }}>
                    <Button
                      icon="tick"
                      text={'Create Order'}
                      onClick={this.props.onCreateOrder}
                      disabled={this.props.disableBtn}
                    />
                  </div>
                </Col>
              </Row>
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }

  private getNameHeader(result: number) {
    return `${this.props.intl.formatMessage({ id: 'delivery.order.open.search_results' })} (${result})`;
  }
}

export default injectIntl(SearchResults);
