import { Tooltip } from '@blueprintjs/core';
import React from 'react';
import { WhiteButton } from '../components/order_management/style';

interface CustomerProps {
  action: any;
}
const CustomComponent: React.FunctionComponent<CustomerProps> = (props: CustomerProps): JSX.Element => {
  return (
    <Tooltip usePortal hoverCloseDelay={0} position={'bottom'} content={'Close'}>
      <WhiteButton
        icon="cross"
        minimal
        onClick={() => {
          props.action();
        }}
      />
    </Tooltip>
  );
};

export default CustomComponent;
