import React, { Component } from 'react';
import { FieldState, FormState } from 'formstate';
import { Col, Row } from 'react-flexbox-grid';
import {
  getFormValue,
  showToastNotification,
  VCardPanel,
  VCheckboxField,
  VDateTimePicker,
  VInputField,
  VSelectField,
  VTextAreaField
} from 'gsi-vx-ui';
import { ILayer } from 'gsi-vx-ui/lib/types/Form/Inputs/ILayer';
import OrderDetailsFees from './order_details_fees/OrderDetailsFees';
import { Button, Classes, Divider } from '@blueprintjs/core';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { onlyNumber, required, uniqueArray } from '../../../commons/Validators';
import { OrderManagementStore } from '../../../stores/OrderManagementStore';
import { inject, observer } from 'mobx-react';
import moment from 'moment';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { OrderModel } from '../../../models/OrderModel';
import styled from 'styled-components';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';

const OrderConsumer = OrderContext.Consumer;
const ThemeConsumer = AppThemeContext.Consumer;
const fees: any[] = [];
const datafees: any[] = [];
const SVDate = styled(VDateTimePicker)`
  & label:before {
    content: '* ';
    padding-top: 1px;
    position: relative;
    bottom: 2px;
    font-size: 12px;
    color: #e21a1a;
    text-align: start;
  }
`;
export const FOrderFeeContext = React.createContext({
  onSelectFee: (e: any) => {},
  onDelFee: (e: any) => {},
  listFees: fees,
  dataFee: fees,
  isLoading: false
});
interface IDetailsForm extends InjectedIntlProps {
  nofees?: number;
  order_management_store?: OrderManagementStore;
  orderDetails: OrderModel;
  onSave(): void;
  onCancel(): void;
}
@inject('order_management_store')
@observer
class OrderDetailsComponent extends Component<IDetailsForm> {
  form!: FormState<any>;

  constructor(props: IDetailsForm) {
    super(props);
    this.initForm();
  }

  initForm = () => {
    const { orderDetails, order_management_store, intl } = this.props;
    moment.locale(intl.locale);
    orderDetails.productId && this.onSelectProduct(orderDetails.productId);
    const date = orderDetails.orderDate ? moment(orderDetails.orderDate).toDate() : moment().toDate();
    this.form = new FormState({
      accountId: new FieldState(orderDetails.accountId || ''),
      acctId: new FieldState(orderDetails.acctId || ''),
      fsname: new FieldState(orderDetails.name || ''),
      fsaddress: new FieldState(orderDetails.address || ''),
      id: new FieldState(orderDetails.orderId || ''),
      ordQty: new FieldState(orderDetails.qty || '').validators(onlyNumber),
      ordDate: new FieldState(date || '').validators(required),
      fscity: new FieldState(orderDetails.city || ''),
      fsStateProv: new FieldState(orderDetails.state || ''),
      tankSize: new FieldState(orderDetails.tanksize || ''),
      productId: new FieldState(orderDetails.productId || ''),
      orderTypeId: new FieldState(orderDetails.orderTypeId || ''),
      fillTypeId: new FieldState(
        orderDetails.fillTypeId || orderDetails.fillTypeId === 0 ? orderDetails.fillTypeId.toString() : ''
      ),
      fixed: new FieldState(''),
      price: new FieldState(orderDetails.price || '').validators(onlyNumber),
      skipContracts: new FieldState(orderDetails.skipContracts),
      remarks: new FieldState(orderDetails.remarks || ''),
      slipOrder: new FieldState(orderDetails.slipOrder),
      orderAdditionalFees: new FieldState(order_management_store!.listDetailsFees).validators(uniqueArray)
    });
  };
  updateForm = () => {
    const { order_management_store } = this.props;
    this.form.$.orderAdditionalFees.onChange(order_management_store!.listDetailsFees);
  };
  componentDidUpdate() {
    this.updateForm();
  }
  onSubmit = (action: any) => {
    const data = getFormValue(this.form);
    this.form.validate().then(res => {
      if (res.hasError) {
        this.form.fieldError === 'Duplicate' &&
          showToastNotification({ type: 'danger', message: 'You have Fees duplicate!', timeout: 1600 });
      } else {
        action(data, this.props.onSave);
      }
    });
  };
  onSelectProduct = (id: any) => {
    this.props.order_management_store!.getFees(id);
  };
  onCancel = (e: any) => {
    e.preventDefault();
    this.props.onCancel();
  };
  onAddFee = (e: any) => {
    this.props.order_management_store!.addFee(e);
  };
  onRemoveFee = (e: any) => {
    this.props.order_management_store!.delFee(e);
  };
  onEditFee = (e: any) => {};
  render() {
    const layer: ILayer = {
      labelWidth: 4,
      inputWidth: 8,
      labelOrientation: 'end',
      inputOrientation: 'start'
    };
    const { intl, order_management_store } = this.props;
    const {
      listProducts,
      listTypes,
      isLoading,
      listSelectFees,
      listFillTypes,
      listDetailsFees
    } = order_management_store!;

    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <OrderConsumer>
              {(orderContext: IOrderContext) =>
                orderContext && (
                  <div style={{ padding: 5, width: '100%' }}>
                    <Row>
                      <Col sm={6} lg={3}>
                        <VCardPanel
                          headerText={'Account Information'}
                          headerTextJustify={'center'}
                          height={'410px'}
                          bodyPadding={'5px'}
                          headerBackgroundColor={themeContext.headers}
                          headerColor={'#FFFFFF'}
                        >
                          <VInputField
                            id={'accountId'}
                            label="Account ID:"
                            type="text"
                            inline
                            className={isLoading ? Classes.SKELETON : ''}
                            fill
                            fieldState={this.form.$.accountId}
                            layer={layer}
                            disabled
                          />
                          <VInputField
                            id={'name'}
                            label="Name:"
                            inline
                            className={isLoading ? Classes.SKELETON : ''}
                            fill
                            type="text"
                            fieldState={this.form.$.fsname}
                            layer={layer}
                            disabled
                          />
                          <VInputField
                            id={'address'}
                            label="Address:"
                            inline
                            className={isLoading ? Classes.SKELETON : ''}
                            type="text"
                            fill
                            layer={layer}
                            fieldState={this.form.$.fsaddress}
                            disabled
                          />
                          <VInputField
                            id={'city'}
                            label="City:"
                            inline
                            className={isLoading ? Classes.SKELETON : ''}
                            fill
                            type="text"
                            layer={layer}
                            fieldState={this.form.$.fscity}
                            disabled
                          />
                          <VInputField
                            id={'state'}
                            label="State/Prov:"
                            inline
                            fill
                            className={isLoading ? Classes.SKELETON : ''}
                            layer={layer}
                            type="text"
                            fieldState={this.form.$.fsStateProv}
                            disabled
                          />
                          <VInputField
                            id={'tank'}
                            label="Tank Size:"
                            inline
                            fill
                            className={isLoading ? Classes.SKELETON : ''}
                            layer={layer}
                            type="text"
                            fieldState={this.form.$.tankSize}
                            disabled
                          />
                          <div className={isLoading ? Classes.SKELETON : ''}>
                            <VSelectField
                              label={'Product:'}
                              id={'products'}
                              onChange={this.onSelectProduct}
                              inline
                              required
                              validators={[required]}
                              fieldState={this.form.$.productId}
                              layer={layer}
                              fill
                              options={listProducts}
                            />
                          </div>
                        </VCardPanel>
                      </Col>
                      <Col sm={6} lg={4}>
                        <VCardPanel
                          headerTextJustify={'center'}
                          headerText={'Order Form'}
                          bodyPadding={'5px'}
                          headerBackgroundColor={themeContext.headers}
                          headerColor={'#FFFFFF'}
                        >
                          <VInputField
                            id={'orderId'}
                            label="Order ID:"
                            inline
                            disabled
                            className={isLoading ? Classes.SKELETON : ''}
                            type="text"
                            fill
                            layer={layer}
                            fieldState={this.form.$.id}
                          />
                          <VInputField
                            id={'orderQty'}
                            label="Order Qty:"
                            inline
                            required
                            className={isLoading ? Classes.SKELETON : ''}
                            validators={[required]}
                            fill
                            layer={layer}
                            type="text"
                            fieldState={this.form.$.ordQty}
                          />
                          <SVDate
                            dateType={'DATE'}
                            label={'Order Date'}
                            id={'orderDate'}
                            className={isLoading ? Classes.SKELETON : ''}
                            validators={[required]}
                            inline
                            layer={layer}
                            leftIcon={'calendar'}
                            fieldState={this.form.$.ordDate}
                          />
                          <div className={isLoading ? Classes.SKELETON : ''}>
                            <VSelectField
                              label={'Order Type:'}
                              id={'orderType'}
                              required
                              inline
                              fieldState={this.form.$.orderTypeId}
                              layer={layer}
                              validators={[required]}
                              fill
                              options={listTypes}
                            />
                          </div>
                          <div className={isLoading ? Classes.SKELETON : ''}>
                            <VSelectField
                              label={'Order Fill Type:'}
                              id={'fillTypeId'}
                              inline
                              required
                              fieldState={this.form.$.fillTypeId}
                              layer={layer}
                              fill
                              options={listFillTypes}
                            />
                          </div>

                          <VInputField
                            id={'fixed'}
                            label="Fixed Amt/Del:"
                            className={isLoading ? Classes.SKELETON : ''}
                            inline
                            fill
                            layer={layer}
                            type="text"
                            fieldState={this.form.$.fixed}
                          />
                          <VInputField
                            id={'price'}
                            label="Price:"
                            className={isLoading ? Classes.SKELETON : ''}
                            inline
                            fill
                            layer={layer}
                            type="text"
                            fieldState={this.form.$.price}
                          />
                          <VCheckboxField
                            layer={{
                              labelWidth: 4,
                              inputWidth: 6,
                              labelOrientation: 'end',
                              inputOrientation: 'start'
                            }}
                            className={isLoading ? Classes.SKELETON : ''}
                            fieldState={this.form.$.skipContracts}
                            alignIndicator={'right'}
                            id="skipContracts"
                            label="Skip Contract"
                            inline
                          />
                        </VCardPanel>
                      </Col>
                      <Col sm={12} lg={5}>
                        <Row>
                          <Col lg={12} sm={6} style={{ marginBottom: 18 }}>
                            <VCardPanel
                              headerText={'Description'}
                              bodyPadding={'5px 5px 5px 5px'}
                              headerTextJustify={'center'}
                              headerBackgroundColor={themeContext.headers}
                              headerColor={'#FFFFFF'}
                            >
                              <VTextAreaField
                                fill
                                layer={{
                                  labelWidth: 4,
                                  inputWidth: 8,
                                  labelOrientation: 'center',
                                  inputOrientation: 'start'
                                }}
                                id="remarks"
                                label="Order Description"
                                inline
                                className={isLoading ? Classes.SKELETON : ''}
                                fieldState={this.form.$.remarks}
                              />
                            </VCardPanel>
                          </Col>
                          <Col lg={12} sm={6}>
                            <FOrderFeeContext.Provider
                              value={{
                                isLoading,
                                onSelectFee: this.onAddFee,
                                listFees: listSelectFees,
                                onDelFee: this.onRemoveFee,
                                dataFee: listDetailsFees
                              }}
                            >
                              <OrderDetailsFees />
                            </FOrderFeeContext.Provider>
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                    <Divider />
                    <Row end={'xs'} style={{ padding: 5 }}>
                      <Col xs>
                        <Button
                          minimal
                          intent={'none'}
                          icon={'tick'}
                          onClick={() => {
                            this.onSubmit(orderContext.onSaveOrders);
                          }}
                          disabled={isLoading}
                          text={intl.formatMessage({
                            id: 'setup.location.operator.filter.apply',
                            defaultMessage: 'Save order'
                          })}
                        />
                        <Button
                          minimal
                          intent={'none'}
                          icon={'disable'}
                          onClick={this.props.onCancel}
                          text={intl.formatMessage({
                            id: 'setup.location.operator.form.cancel',
                            defaultMessage: 'Cancel'
                          })}
                        />
                      </Col>
                    </Row>
                  </div>
                )
              }
            </OrderConsumer>
          )
        }
      </ThemeConsumer>
    );
  }
}

export default injectIntl(OrderDetailsComponent);
