import React from 'react';
import { VTable } from 'gsi-vx-ui';
import { IListTable } from '../../../type/OrderManagementType';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';
import { injectIntl } from 'react-intl';
import { upperCase } from 'lodash';

const OrderConsumer = OrderContext.Consumer;

const ListIncompleteComponent = (props: IListTable) => {
  const { orders, onRowClick } = props;
  return (
    <OrderConsumer>
      {(orderContext: IOrderContext) =>
        orderContext && (
          <VTable
            cellSelectionType={'ENTIRE_ROW'}
            striped
            actionsSelection={{ onSelectionChange: onRowClick }}
            columns={props.columns}
            widgetsCell={orderContext.renderCustom(props.columns)}
            typeHeightRow={'HALF'}
            enableColumnResizing={true}
            columns_name={{
              orderId: props.intl.formatMessage({ id: 'delivery.order.review.cmOrdId' }),
              accountId: props.intl.formatMessage({ id: 'delivery.order.review.cmAccId' }),
              lastDate: props.intl.formatMessage({ id: 'delivery.order.review.cmLastDate' }),
              name: upperCase(props.intl.formatMessage({ id: 'delivery.order.open.name' })),
              error: props.intl.formatMessage({ id: 'delivery.order.open.cmError' }),
              type: upperCase(props.intl.formatMessage({ id: 'delivery.order.open.order_type' })),
              orderDate: upperCase(props.intl.formatMessage({ id: 'delivery.order.open.order_date' })),
              qty: upperCase(props.intl.formatMessage({ id: 'delivery.order.open.order_qty' }))
            }}
            data={orders}
          />
        )
      }
    </OrderConsumer>
  );
};

export default injectIntl(ListIncompleteComponent);
