import React, { Component } from 'react';
import { disposeOnUnmount, inject, observer } from 'mobx-react';
import { VCardPanel } from 'gsi-vx-ui';
import { Dialog } from '@blueprintjs/core';
import ListUngeoOrders from './ListUngeoOrders';
import { UnGeoStore } from '../../../stores/UnGeoStore';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import OrderDetailsComponent from '../order_details/OrderDetailsComponent';
import CustomComponent from '../../../commons/CustomComponent';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import { reaction } from 'mobx';

const ThemeConsumer = AppThemeContext.Consumer;

const OrderConsumer = OrderContext.Consumer;

interface IUnGeoOrdersComponent extends InjectedIntlProps {
  ungeo_store?: UnGeoStore;
  days: number | string;
  filter: boolean;
}

@inject('ungeo_store')
@observer
class UnGeoOrdersComponent extends Component<IUnGeoOrdersComponent> {
  constructor(props: IUnGeoOrdersComponent) {
    super(props);
    this.onChangeFilter();
    this.onChangeDays();
    disposeOnUnmount(this, [
      reaction(
        () => this.props.filter,
        () => {
          this.onChangeFilter();
        }
      ),
      reaction(
        () => this.props.days,
        () => {
          this.onChangeDays();
        }
      )
    ]);
  }
  onSelectOrder = (data: any) => {
    if (data) {
      this.props.ungeo_store!.setElement(data);
    } else {
      this.props.ungeo_store!.resetElement();
    }
  };
  onChangeDays = () => {
    this.props.days
      ? this.props.ungeo_store!.addFilter({
          value: this.props.days,
          field: 'numberDate',
          operator: 'EQUALITY'
        })
      : this.props.ungeo_store!.removeFilter({
          value: '',
          field: 'numberDate',
          operator: 'EQUALITY'
        });
  };
  componentWillUnmount() {
    this.props.ungeo_store!.resetElement();
    this.props.ungeo_store!.setOpenDetails(false);
  }
  componentDidMount() {}
  handleSaveDetails = () => {
    const { ungeo_store } = this.props;
    ungeo_store!.setOpenDetails(false);
    ungeo_store!.getElements();
  };
  onChangeFilter = () => {
    this.props.filter && this.props.ungeo_store!.updateFilterType();
    this.props.ungeo_store!.setFilterBySummary(this.props.filter);
  };
  render() {
    const { ungeo_store } = this.props;
    const { elements, count, isLoading, openDetails } = ungeo_store!;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <div style={{ margin: 5 }}>
              <OrderConsumer>
                {(orderContext: IOrderContext) =>
                  orderContext && (
                    <ListUngeoOrders
                      columns={['type', 'geo', 'orderId', 'accountId', 'name', 'lastDate', 'orderDate', 'qty']}
                      orders={elements}
                      onSelect={(data: any) => {
                        this.onSelectOrder(data);
                        orderContext.onSelectOrders(data);
                      }}
                      noorders={orderContext.totalOrders}
                      noungeoorders={count}
                      isLoading={isLoading}
                    />
                  )
                }
              </OrderConsumer>
              {openDetails && (
                <Dialog
                  isOpen={true}
                  canOutsideClickClose={false}
                  onClose={() => ungeo_store!.setOpenDetails(false)}
                  style={{ width: '85%', paddingBottom: '0' }}
                  canEscapeKeyClose={false}
                >
                  <VCardPanel
                    headerText={this.props.intl.formatMessage({ id: 'delivery.order.open.titleDetails' })}
                    bodyPadding={'1px'}
                    headerBackgroundColor={themeContext.headers}
                    headerColor={'#FFFFFF'}
                    headerTextJustify={'center'}
                    headerCustomComponent={<CustomComponent action={() => ungeo_store!.setOpenDetails(false)} />}
                  >
                    <OrderDetailsComponent
                      orderDetails={ungeo_store!.element}
                      onSave={this.handleSaveDetails}
                      onCancel={() => ungeo_store!.setOpenDetails(false)}
                      nofees={0}
                    />
                  </VCardPanel>
                </Dialog>
              )}
            </div>
          )
        }
      </ThemeConsumer>
    );
  }
}

export default injectIntl(UnGeoOrdersComponent);
