import React, { Component } from 'react';
import { OpenOrdersStore } from '../../../stores/OpenOrdersStore';
import { VCardPanel, VSpinner } from 'gsi-vx-ui';
import OrderDetailsComponent from '../order_details/OrderDetailsComponent';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { disposeOnUnmount, inject, observer } from 'mobx-react';
import { AppThemeContext, ITheme } from '../../../../../../../App';
import ListOpenOrdersComponent from '../open/ListOpenOrdersComponent';
import CustomComponent from '../../../commons/CustomComponent';
import { Dialog } from '@blueprintjs/core';
import { reaction } from 'mobx';
import { IOrderContext, OrderContext } from '../../../containers/OrderManagementContainer';

const OrderConsumer = OrderContext.Consumer;
const ThemeConsumer = AppThemeContext.Consumer;

interface IOpenOrdersComponent extends InjectedIntlProps {
  open_orders_store?: OpenOrdersStore;
  days: number | string;
  filter: boolean;
}

@inject('open_orders_store')
@observer
class OpenOrdersComponent extends Component<IOpenOrdersComponent> {
  constructor(props: IOpenOrdersComponent) {
    super(props);
    this.onChangeFilter();
    this.onChangeDays();
    disposeOnUnmount(this, [
      reaction(
        () => this.props.filter,
        () => {
          this.onChangeFilter();
        }
      ),
      reaction(
        () => this.props.days,
        () => {
          this.onChangeDays();
        }
      )
    ]);
  }
  handleOpenDetails() {
    this.props.open_orders_store!.setOpenDetails(true);
  }
  onSelectOpen(data: any) {
    this.props.open_orders_store!.setElement(data);
  }
  onChangeDays = () => {
    this.props.days
      ? this.props.open_orders_store!.addFilter({
          value: this.props.days,
          field: 'numberDate',
          operator: 'EQUALITY'
        })
      : this.props.open_orders_store!.removeFilter({
          value: '',
          field: 'numberDate',
          operator: 'EQUALITY'
        });
  };
  componentWillUnmount() {
    this.props.open_orders_store!.resetElement();
  }
  handleSaveDetails() {
    const { open_orders_store } = this.props;
    open_orders_store!.setOpenDetails(false);
    open_orders_store!.getElements();
  }
  onChangeFilter = () => {
    this.props.filter && this.props.open_orders_store!.updateFilterType();
    this.props.open_orders_store!.setFilterByUrgency(this.props.filter);
  };
  handleCloseDetails() {
    this.props.open_orders_store!.setOpenDetails(false);
  }
  render() {
    const { isLoading, isDetailsOpen, elements, count, element } = this.props.open_orders_store!;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <OrderConsumer>
              {(orderContext: IOrderContext) =>
                orderContext && (
                  <VCardPanel
                    headerText={`${this.props.intl.formatMessage({
                      id: 'delivery.order.open.title_list_open'
                    })} (${count} of ${orderContext.totalOrders})`}
                    bodyPadding={'1px'}
                    headerBackgroundColor={themeContext.headers}
                    headerColor={'#FFFFFF'}
                    headerTextJustify={'center'}
                  >
                    {isLoading ? (
                      <VSpinner />
                    ) : (
                      <ListOpenOrdersComponent
                        orders={elements}
                        columns={['type', 'geo', 'orderId', 'accountId', 'name', 'lastDate', 'orderDate', 'qty']}
                        renderCustomerType={orderContext.renderCustomerType}
                        onSelect={this.onSelectOpen.bind(this)}
                      />
                    )}
                    {isDetailsOpen && (
                      <Dialog
                        isOpen={isDetailsOpen}
                        canOutsideClickClose={false}
                        onClose={this.handleCloseDetails.bind(this)}
                        style={{ width: '85%', paddingBottom: '0' }}
                        canEscapeKeyClose={false}
                      >
                        <VCardPanel
                          headerText={this.props.intl.formatMessage({ id: 'delivery.order.open.titleDetails' })}
                          bodyPadding={'1px'}
                          headerBackgroundColor={themeContext.headers}
                          headerColor={'#FFFFFF'}
                          headerTextJustify={'center'}
                          headerCustomComponent={<CustomComponent action={this.handleCloseDetails.bind(this)} />}
                        >
                          <OrderDetailsComponent
                            orderDetails={element}
                            onSave={this.handleSaveDetails.bind(this)}
                            onCancel={this.handleCloseDetails.bind(this)}
                            nofees={0}
                          />
                        </VCardPanel>
                      </Dialog>
                    )}
                  </VCardPanel>
                )
              }
            </OrderConsumer>
          )
        }
      </ThemeConsumer>
    );
  }
}

export default injectIntl(OpenOrdersComponent);
