import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Button, Classes } from '@blueprintjs/core';
import { Col, Row } from 'react-flexbox-grid';
import { VInputField, VSelectField } from 'gsi-vx-ui';
import { FieldState, FormState } from 'formstate';
import ListDeliveryTypeComponent from './delivery_type/ListDeliveryTypeComponent';
import ListOrdersComponent from './list_orders/ListOrdersComponent';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { OrderManagementStore } from '../../stores/OrderManagementStore';
import { BtnAddProfile } from '../order_management/style';
import { debounce } from 'lodash';
import { OrdersStore } from '../../stores/OrdersStore';
import BottomActionsComponent from './bottom_actions/BottomActionsComponent';
import { styleButton } from './style';
import DialogFilterComponent from './dialog_filter/DialogFilterComponent';
import DialogCreateAccount from './dialog_create_account/DialogCreateAccount';
import { IOrderContext, OrderContext } from '../../containers/OrderManagementContainer';

const OrderConsumer = OrderContext.Consumer;

interface IOrdersComponent extends InjectedIntlProps {
  order_management_store?: OrderManagementStore;
  orders_store?: OrdersStore;
}

@inject('order_management_store', 'orders_store')
@observer
class OrdersComponent extends Component<IOrdersComponent> {
  form!: FormState<any>;
  constructor(props: IOrdersComponent) {
    super(props);
    this.initForm();
  }
  initForm = () => {
    const { search, profileActive } = this.props.order_management_store!;
    this.form = new FormState({
      select_profile: new FieldState(profileActive),
      chkAllOrder: new FieldState(''),
      chkUngeocodedOnly: new FieldState(''),
      fSearch: new FieldState(search),
      startOrderDate: new FieldState(''),
      endOrderDate: new FieldState(''),
      days: new FieldState('')
    });
  };
  componentWillUnmount() {
    this.props.order_management_store!.setOrderDetails({});
  }

  changeFilter = (data: boolean) => {
    this.props.orders_store!.changeFilterByUrgency(data);
  };
  raiseDoSearchWhenUserStoppedTyping = debounce((setSearchList: any) => {
    setSearchList(this.form.$.fSearch.value);
  }, 1800);
  render() {
    const { order_management_store, orders_store, intl } = this.props;
    const { isLoading, listProfiles } = order_management_store!;
    const { activeList, filterByUrgency, searchDays } = orders_store!;
    return (
      <OrderConsumer>
        {(orderContext: IOrderContext) =>
          orderContext && (
            <React.Fragment>
              <div style={{ padding: 5 }}>
                <Row>
                  <Col lg={9} style={{ marginBottom: 5 }}>
                    <ListDeliveryTypeComponent
                      onChangeFilter={this.changeFilter}
                      selType={orderContext.isOrdersLoading ? 'DISABLED' : 'CELL'}
                    />
                  </Col>
                  <Col lg={3} style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
                    <div style={{ width: 210 }}>
                      <VInputField
                        onChange={(data: any) => {
                          this.form.$.fSearch.onChange(data);
                          this.raiseDoSearchWhenUserStoppedTyping(orderContext.setSearchList);
                        }}
                        fill
                        id={'idsearchorder'}
                        fieldState={this.form.$.fSearch}
                        placeholder={'Ticket Search...'}
                        type={'search'}
                        leftIcon={'search'}
                      />
                    </div>
                    <div style={{ width: 250, display: 'flex', justifyContent: 'flex-end' }}>
                      <div style={{ width: 175, height: 35 }}>
                        <VSelectField
                          id={'idselectprofile'}
                          className={isLoading ? Classes.SKELETON : ''}
                          fieldState={this.form.$.select_profile}
                          defaultText={intl.formatMessage({ id: 'delivery.order.open.select_profile' })}
                          layer={{ inputOrientation: 'end' }}
                          disabled={orderContext.isOrdersLoading}
                          onChange={orderContext.selectProfile}
                          fill
                          options={listProfiles}
                        />
                      </div>
                      <BtnAddProfile
                        icon="filter-remove"
                        disabled={orderContext.isOrdersLoading}
                        onClick={() => {
                          this.form.$.select_profile.onChange('');
                          orderContext.clearProfile();
                        }}
                      />
                    </div>
                    <DialogFilterComponent />
                    <Button icon={'search'} style={styleButton} disabled>
                      {intl.formatMessage({ id: 'delivery.order.open.btn_analyze' })}
                    </Button>
                    <DialogCreateAccount />
                  </Col>
                </Row>
              </div>
              <div style={{ padding: 5 }}>
                <ListOrdersComponent
                  total={orderContext.totalOrders}
                  activeList={activeList}
                  filterByUrgency={filterByUrgency}
                  searchDays={searchDays}
                />
              </div>
              <Row style={{ marginTop: 7 }}>
                <BottomActionsComponent isLoading={orderContext.isOrdersLoading} searchDays={orderContext.searchDays} />
              </Row>
            </React.Fragment>
          )
        }
      </OrderConsumer>
    );
  }
}
export default injectIntl(OrdersComponent);
