import React, { Component } from 'react';
import { VCardPanel, VInputField, VSelectField } from 'gsi-vx-ui';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { FormState } from 'formstate';
import { ILayer } from 'gsi-vx-ui/lib/types/Form/Inputs/ILayer';
import SearchCriteriaActions from '../search_criteria_actions/SearchCriteriaActions';
import { AppThemeContext, ITheme } from '../../../../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;
interface ISearchAccountForm extends InjectedIntlProps {
  form: FormState<any>;
  products: any[];
  states: any[];
}

class SearchCriteriaAccountForm extends Component<ISearchAccountForm> {
  constructor(props: ISearchAccountForm) {
    super(props);
  }

  render() {
    const { intl, form, states, products } = this.props;
    const layer: ILayer = {
      labelWidth: 4,
      inputWidth: 8,
      labelOrientation: 'end',
      inputOrientation: 'start'
    };
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              headerText={intl.formatMessage({ id: 'delivery.order.open.searchCriteria' })}
              bodyPadding={'5px'}
              height={'315px'}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#FFFFFF'}
              headerCustomComponent={<SearchCriteriaActions />}
            >
              <VInputField
                id={'account'}
                fieldState={form.$.accountId}
                fill
                inline
                layer={layer}
                label={'Account Id'}
              />
              <VInputField id={'name'} fieldState={form.$.name} fill inline layer={layer} label={'Name'} />
              <VSelectField
                label={'Product:'}
                id={'products'}
                inline
                fieldState={form.$.productId}
                layer={layer}
                fill
                options={products}
              />
              <VInputField id={'city'} fieldState={form.$.city} fill inline layer={layer} label={'City'} />
              <VSelectField
                label={'State/Prov:'}
                id={'stateProv'}
                inline
                fieldState={form.$.stateProv}
                layer={layer}
                fill
                options={states}
              />
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }
}

export default injectIntl(SearchCriteriaAccountForm);
